import React from "react";
import SimpleCheckboxGroup from "./SimpleCheckboxGroup";

export type CheckboxOption = {
  label: string;
  checked: boolean;
  count?: number;
  image?: JSX.Element;
};

export type CheckboxGroupProps = {
  variant: string;
  options: CheckboxOption[];
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  groupLabel?: string;
  hintText?: string;
  error?: boolean;
};

type Element = {
  [property: string]: JSX.Element;
};

const CheckboxGroup = ({
  variant,
  options,
  onChange,
  error,
  groupLabel,
  hintText,
}: CheckboxGroupProps) => {
  const element: Element = {
    simple: (
      <SimpleCheckboxGroup
        options={options}
        onChange={onChange}
        groupLabel={groupLabel}
        hintText={hintText}
        error={error}
      />
    ),
  };

  return element[variant];
};

export default CheckboxGroup;
