/**
 * `useHover` detects whether the mouse is hovering an element by returning a
 * `ref` you can pass to a target element and a `isHovered` boolean.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const hoverRef = useRef();
 *   const isHovered = useHover(hoverRef);
 *
 *   return (
 *     <button ref={hoverRef}>
 *       {isHovered ? "Hovered" : "Hover me"}
 *     </div>
 *   );
 * };
 * ```
 *
 * Credit: https://usehooks.com/useHover/
 */

import { RefObject, useEffect, useState } from "react";

const useHover = <T extends Element>(element: RefObject<T>) => {
  const [hovered, setHovered] = useState(false);

  const handleMouseOver = () => setHovered(true);
  const handleMouseOut = () => setHovered(false);

  useEffect(() => {
    const node = element.current;
    const isSupported = node && node.addEventListener;
    if (!isSupported) {
      return;
    }
    if (node) {
      node.addEventListener("mouseover", handleMouseOver);
      node.addEventListener("mouseout", handleMouseOut);
      return () => {
        node.removeEventListener("mouseover", handleMouseOver);
        node.removeEventListener("mouseout", handleMouseOut);
      };
    }
  }, [element]);

  return hovered;
};

export default useHover;
