import { ClassNameMap } from "@mui/material";
import { makeStyles } from "tss-react/mui";
import type { Animation } from "./index";

type useSpriteAnimationProps = { animation: Animation; segment: string };

type segmentedSpriteStyleFunction = ({
  animation,
  segment,
}: useSpriteAnimationProps) => {
  [key: string]: any;
};

type SegmentedSpriteClasses = {
  spriteContainer: segmentedSpriteStyleFunction;
  sprite: segmentedSpriteStyleFunction;
  start: segmentedSpriteStyleFunction;
  animate: segmentedSpriteStyleFunction;
  end: segmentedSpriteStyleFunction;
};

export type SpriteSegments = {
  [key: string]: ClassNameMap<keyof SegmentedSpriteClasses>;
};

const makeAnimationSegmentStyles = makeStyles<useSpriteAnimationProps>({
  name: "MuiDWSpriteAnimation",
})((_theme, { animation: a, segment: s }) => ({
  spriteContainer: {
    overflow: a.data.debug ? "" : "hidden",
    width: `${a.data.frameWidth}px`,
    height: `${a.data.frameHeight}px`,
    outline: a.data.debug ? "2px dashed #0000FF50" : "initial", //use initial because "" breaks node_modules/cssom/lib/parse.js
    outlineOffset: a.data.debug ? "1px" : "initial",
  },
  sprite: {
    transform: `${a.segments[s].startOffsetPX}px 0`,
    width: a.data.frameWidth * a.data.totalFrames,
    height: a.data.frameHeight,
    outline: a.data.debug ? "1px dashed #ff000050" : "initial",
  },
  start: {
    transform: `translate(${a.segments[s].startOffsetPX}px, 0)`,
  },
  animate: {
    transition: `transform ${a.segments[s].durationSEC}s steps(${a.segments[s].framesToPlay})`,
    transform: `translate(${a.segments[s].finalFramePX}px, 0)`,
  },
  end: {
    transform: `translate(${a.segments[s].finalFramePX}px, 0)`,
  },
}));

const useSpriteAnimation = (animation: Animation) => {
  let result: SpriteSegments = {};
  for (const segment in animation.segments) {
    const { classes } = makeAnimationSegmentStyles({
      animation: animation,
      segment: segment,
    });
    result[segment] = classes;
  }
  return result;
};

export { useSpriteAnimation };
