import { Loader } from "@googlemaps/js-api-loader";
import { cancelable } from "cancelable-promise";
import { State } from "constants/states";
import useStorage from "hooks/useStorage";
import {
  createContext,
  Dispatch,
  ReactNode,
  useCallback,
  useEffect,
  useReducer,
  useState,
} from "react";

const defaultPriorities: GeoMethod[] = [
  "user_override",
  "user_address",
  "ip_address",
];

type Props = {
  timeZoneApiKey: string;
  geoApiKey: string;
  homeAddress?: Partial<Address>;
  children: ReactNode;
};

type GeocoderRequest = google.maps.GeocoderRequest;
type GeocoderResult = google.maps.GeocoderResult;
type GeocoderStatus = google.maps.GeocoderStatus;

type GeoMethod = "user_override" | "user_address" | "ip_address" | "browser";

type Coords = {
  lat: number;
  lng: number;
};

type Address = {
  line1: string;
  line2?: string;
  city: string;
  state: State;
  postalCode: string;
};

type GeoAddress = Partial<Address> & Partial<Coords>;

type GeoState = {
  method?: null | GeoMethod;
  locations: GeoAddress[];
  userOverride: null | GeoAddress;
};

type GeoContextState = {
  timeZoneApiKey?: string;
  geoApiKey?: string;
  homeAddress?: GeoAddress;
  geoState: GeoState;
  geoDispatch: Dispatch<GeoAction>;
  google?: typeof google;
};

type GeoSetLocations = [
  "setLocations",
  { method: GeoMethod; locations: GeoAddress[] },
];
type GeoSetUserOverride = [
  "setUserOverride",
  { userOverride: null | GeoAddress },
];
type GeoClear = ["clear"];

type GeoAction = GeoSetLocations | GeoSetUserOverride | GeoClear;

const initialGeoState: GeoState = {
  method: null,
  locations: [],
  userOverride: null,
};

// Converts address object into google-compatible string
const stringifyAddress = (address: Partial<Address>): string => {
  const { line1, city, state, postalCode } = address;
  const addrString = [line1, city, state]
    .filter((item) => item)
    .map((item) => item!.toLowerCase())
    .join(", ");
  return [addrString, postalCode].filter((item) => item).join(" ");
};

const stub = (): never => {
  throw new Error("You forgot to wrap your component in <GeoProvider>/");
};

const initialContext: GeoContextState = {
  geoState: initialGeoState,
  geoDispatch: stub,
};

const GeoContext = createContext(initialContext);

const GeoProvider = ({
  timeZoneApiKey,
  geoApiKey,
  homeAddress,
  children,
}: Props) => {
  const [googleLib, setGoogleLib] = useState<undefined | typeof google>(
    typeof window !== "undefined" ? window.google : undefined,
  );
  const [userOverride, setUserOverride] = useStorage<null | GeoAddress>(
    "locationOverride",
  );

  const geoReducer = useCallback(
    (state: GeoState, [type, payload]: GeoAction) => {
      switch (type) {
        case "setLocations":
          return {
            ...state,
            method: payload.method,
            locations: payload.locations,
          };
        case "setUserOverride":
          setUserOverride(payload.userOverride);
          return {
            ...state,
            method: null,
            locations: [],
            userOverride: payload.userOverride,
          };
        case "clear":
          return initialGeoState;
        default:
          throw new Error("Invalid GeoAction");
      }
    },
    [setUserOverride],
  );

  const [geoState, geoDispatch] = useReducer(geoReducer, {
    ...initialGeoState,
    userOverride,
  });

  useEffect(() => {
    if (googleLib) {
      return;
    }
    const loader = new Loader({ apiKey: geoApiKey, version: "quarterly" });
    const loadGoogle = cancelable(loader.load())
      .then(() => setGoogleLib(window.google))
      .catch((err: unknown) => {
        if (err instanceof Error) {
          console.error(err.message);
        }
      });
    return () => {
      loadGoogle.cancel();
    };
  }, [geoApiKey, googleLib]);

  return (
    <GeoContext.Provider
      value={{
        timeZoneApiKey,
        geoApiKey,
        homeAddress,
        geoState,
        geoDispatch,
        google: googleLib,
      }}
    >
      {children}
    </GeoContext.Provider>
  );
};

export default GeoProvider;
export {
  GeoContext,
  initialContext as initialLocationContext,
  defaultPriorities,
  stringifyAddress,
};
export type {
  Props as GeoProviderProps,
  Address,
  GeoAddress,
  GeoMethod,
  State,
  Coords,
  GeocoderResult,
  GeocoderStatus,
  GeocoderRequest,
};
