import {
  Slider as MuiSlider,
  SliderProps as MuiSliderProps,
} from "@mui/material";
import { Typography } from "comp-lib/Typography";
import React from "react";
import FormatValue from "util/formatValue";
import useStyles from "./Slider.styles";
import SliderThumb from "./SliderThumb";

type C = React.ElementType;

type SliderProps = Omit<MuiSliderProps<C, { component?: C }>, "onChange"> & {
  onChange: (arg0: any, arg1: any) => void;
  subHeader?: string;
  minMaxLabels?: number[];
};

const Slider = ({
  subHeader,
  minMaxLabels,
  className,
  ...sliderProps
}: SliderProps) => {
  const { classes, cx } = useStyles();

  return (
    <>
      {subHeader && (
        <Typography className={classes.subHeader} variant="bodySmall">
          {subHeader}
        </Typography>
      )}
      <MuiSlider
        components={{
          Thumb: SliderThumb,
        }}
        className={cx([classes.base, className])}
        {...sliderProps}
      />
      {minMaxLabels && (
        <div className={classes.minMaxLabelsContainer}>
          <Typography variant="microcopy">
            {FormatValue(minMaxLabels[0], {
              thousandSeparator: true,
              prefix: "$",
              displayType: "text",
            })}
          </Typography>
          <Typography variant="microcopy">
            {FormatValue(minMaxLabels[1], {
              thousandSeparator: true,
              prefix: "$",
              displayType: "text",
            })}
          </Typography>
        </div>
      )}
    </>
  );
};

export default Slider;
