import { Grid, RadioGroup } from "@mui/material";
import { Typography } from "comp-lib/Typography";
import { useCallback, useEffect, useState } from "react";
import {
  OutlinedRadioButton,
  OutlinedRadioButtonProps,
} from "./OutlinedRadioButton";
import useStyles from "./OutlinedRadioButtonGroup.styles";

export type OutlinedRadioOption<TValue> = Omit<
  OutlinedRadioButtonProps<TValue>,
  "checked" | "onClick"
>;

export type OutlinedRadioButtonGroupProps<TValue> = {
  /**
   * Array of option values to be presented. Can be any type (TValue).
   * The onChange function will receive the selected options value prop when selected
   */
  options: OutlinedRadioOption<TValue>[];
  /**
   * Callback that fires when the selection changes.
   * The value prop (TValue) of the selected option will be passed in as the argument
   * @param value Value of the selected button
   * @returns void
   */
  onChange: (value: TValue) => void;
  /**
   * Provide this optional prop to force the group into a state with the particular value selected
   */
  selectedValue?: TValue;
  /**
   * Disables the entire group of controls
   */
  disabled?: boolean;
  /**
   * Id applied to the group
   */
  id?: string;
  /**
   * Controls how many buttons line up together, controlled via grid.
   */
  buttonsPerRow?: number;
  /**
   * Label appearing above the whole group
   */
  groupLabel?: string;
  /**
   * Controls size of the button
   */
  buttonSize?: "medium" | "large";
  /**
   * className to be applied to the container
   */
  className?: string;
  /**
   * Whether to apply bold formatting to the groupLabel
   */
  boldLabelHeading?: boolean;
};

export function OutlinedRadioButtonGroup<TValue>(
  props: OutlinedRadioButtonGroupProps<TValue>,
) {
  const {
    options,
    onChange,
    groupLabel,
    selectedValue: selectedValueFromProps,
    buttonsPerRow,
    boldLabelHeading = true,
    buttonSize = "medium",
    className,
  } = props;

  const { classes } = useStyles({ buttonsPerRow });
  const [selectedValue, setSelectedValue] = useState<TValue | null>(
    selectedValueFromProps ?? null,
  );
  const isLarge = buttonSize === "large";

  useEffect(() => {
    setSelectedValue(selectedValueFromProps ?? null);
  }, [selectedValueFromProps]);

  const handleClick = useCallback(
    (value: TValue) => {
      setSelectedValue(value);
      onChange(value);
    },
    [onChange],
  );

  return (
    <RadioGroup row value={selectedValue} className={className}>
      <Typography
        variant={boldLabelHeading ? "bodyBold" : "body"}
        className={classes.heading}
      >
        {groupLabel}
      </Typography>
      <Grid spacing={1} container>
        {options.map((option) => {
          return (
            <Grid
              item
              xs={isLarge ? 6 : 4}
              sm={isLarge ? 3 : 2}
              md={isLarge ? 3 : 2}
              lg={isLarge ? 3 : 2}
              xl={isLarge ? 3 : 4}
              key={option.mainLabel}
              className={classes.gridItem}
            >
              <OutlinedRadioButton
                {...option}
                checked={selectedValue === option.value}
                tabIndex={0}
                onClick={handleClick}
              />
            </Grid>
          );
        })}
      </Grid>
    </RadioGroup>
  );
}
