import { Grid, RadioGroup } from "@mui/material";
import { RadioButton } from "comp-lib/RadioButton";
import type {
  RadioButtonGroupV1Props,
  RadioGroupOptions,
} from "comp-lib/RadioButtonGroup/v1";
import { Typography } from "comp-lib/Typography";
import { ChangeEvent, useEffect, useState } from "react";
import useStyles from "./OutlinedGroup.styles";

const OutlinedGroup = (props: RadioButtonGroupV1Props) => {
  const {
    variant,
    onChange,
    options,
    groupTextContent,
    totalButtons,
    boldLabelHeading = true,
  } = props;

  const styleProps = { totalButtons: totalButtons ?? totalButtons };

  const { classes } = useStyles(styleProps);
  const [value, setValue] = useState("");
  const isLarge = variant === "outlinedLarge";

  useEffect(() => {
    setValue(groupTextContent.selectedValue || "");
  }, [groupTextContent.selectedValue]);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const value = (event.target as HTMLInputElement).value;
    setValue(value);
    onChange(value);
  };

  const children = (
    <>
      {options?.map((option: RadioGroupOptions) => {
        const radioProps = {
          ...option.radioButtonProps,
          ...{ tabIndex: 0 },
        };

        return (
          <Grid
            item
            xs={isLarge ? 6 : 4}
            sm={isLarge ? 3 : 2}
            md={isLarge ? 3 : 2}
            lg={isLarge ? 3 : 2}
            xl={isLarge ? 3 : 4}
            key={option.value}
            className={classes.gridItem}
          >
            <RadioButton
              variant={variant}
              value={option.value}
              textContent={option.textContent}
              radioButtonProps={radioProps}
              withSuccessIcon={option.withSuccessIcon}
            />
          </Grid>
        );
      })}
    </>
  );

  return (
    <RadioGroup row value={value} onChange={handleChange}>
      <Typography
        variant={boldLabelHeading ? "bodyBold" : "body"}
        className={classes.heading}
      >
        {groupTextContent.groupLabel}
      </Typography>
      <Grid spacing={1} container>
        {children}
      </Grid>
    </RadioGroup>
  );
};

export default OutlinedGroup;
