import { Accordion, AccordionDetails, AccordionSummary } from "@mui/material";
import { ChevronDownIcon } from "assets/icons";
import { ChangeEvent, ReactNode, useEffect, useState } from "react";
import useStyles from "./DrivewayAccordion.styles";

const emptyArray: number[] = [];

type Summary = ReactNode | ((isOpen: boolean) => ReactNode);

export type DrivewayAccordionProps = {
  summaries: Summary[];
  details: ReactNode[];
  id: string;
  allowMultipleOpen?: boolean;
  expanded?: null | number[];
  onExpansionChange?: (expanded: number[]) => void;
  className?: string;
  accordionContainerClassName?: string;
  summaryClassName?: string;
  detailsClassName?: string;
};

function DrivewayAccordion({
  summaries,
  details,
  id,
  allowMultipleOpen = false,
  expanded = emptyArray,
  onExpansionChange,
  className = "",
  accordionContainerClassName = "",
  summaryClassName = "",
  detailsClassName = "",
}: DrivewayAccordionProps) {
  const { classes, cx } = useStyles();
  const [currentlyExpanded, setCurrentlyExpanded] = useState<number[]>([]);

  useEffect(() => {
    if (onExpansionChange) {
      onExpansionChange(currentlyExpanded);
    }
  }, [currentlyExpanded, onExpansionChange]);

  useEffect(() => {
    if (expanded) {
      setCurrentlyExpanded(allowMultipleOpen ? expanded : [expanded[0]]);
    }
  }, [allowMultipleOpen, expanded]);

  const handleChange =
    (id: number) => (_event: ChangeEvent<unknown>, newExpanded: boolean) => {
      if (newExpanded) {
        setCurrentlyExpanded(
          allowMultipleOpen ? [...currentlyExpanded, id] : [id],
        );
      } else {
        setCurrentlyExpanded(currentlyExpanded.filter((i) => i !== id));
      }
    };

  return (
    <div className={className} id={id}>
      {summaries.map((summary, i) => {
        if (!summary) {
          return null;
        }
        const itemId = `${id}_${i}`;
        const isExpanded = currentlyExpanded.includes(i);
        return (
          <Accordion
            square
            expanded={isExpanded}
            onChange={handleChange(i)}
            className={cx(classes.root, accordionContainerClassName)}
            defaultExpanded={false}
            key={itemId}
          >
            <AccordionSummary
              aria-controls={`${itemId}-details`}
              className={cx(
                classes.accordionSummary,
                summaryClassName,
                "accordion-element",
                isExpanded ? "accordion-open" : "accordion-closed",
              )}
              expandIcon={<ChevronDownIcon className={classes.accordion} />}
              id={`${itemId}-summary`}
            >
              {typeof summary === "function" ? summary(isExpanded) : summary}
            </AccordionSummary>
            <AccordionDetails
              className={cx(classes.accordionDetails, detailsClassName)}
              id={`${itemId}-details`}
            >
              {details[i] || null}
            </AccordionDetails>
          </Accordion>
        );
      })}
    </div>
  );
}
export default DrivewayAccordion;
