/**
 * `useScript` allows injecting arbitrary third-party scripts into the DOM on
 * component render while returning the current loading status of the script.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const status = useScript("https://example.com/cool-script.js");
 *
 *   return (
 *     <div>
 *     {status === "ready" && (
 *       <div>
 *         {window.COOL_SCRIPT.do_thing()}
 *       </div>
 *     )}
 *     </div>
 *   );
 * };
 * ```
 *
 * Credit: https://usehooks-ts.com/react-hook/use-script
 */
import { useEffect, useState } from "react";

export type UseScriptStatus = "idle" | "loading" | "ready" | "error";
export type UseScriptElement = HTMLScriptElement | null;

function useScript(src: string): UseScriptStatus {
  const [status, setStatus] = useState<UseScriptStatus>("loading");

  useEffect(
    () => {
      // Fetch existing script element by src
      // It may have been added by another instance of this hook
      let script: UseScriptElement = document.querySelector(
        `script[src="${src}"]`
      );
      if (!script) {
        // Create script
        script = document.createElement("script");
        script.src = src;
        script.async = true;
        script.setAttribute("data-status", "loading");
        // Add script to document body
        document.body.appendChild(script);
        // Store status in attribute on script
        // This can be read by other instances of this hook
        const setAttributeFromEvent = (event: Event) => {
          if (script) {
            script.setAttribute(
              "data-status",
              event.type === "load" ? "ready" : "error"
            );
          }
        };
        script.addEventListener("load", setAttributeFromEvent);
        script.addEventListener("error", setAttributeFromEvent);
      } else {
        // Grab existing script status from attribute and set to state.
        setStatus(script.getAttribute("data-status") as UseScriptStatus);
      }
      // Script event handler to update status in state
      // Note: Even if the script already exists we still need to add
      // event handlers to update the state for *this* hook instance.
      const setStateFromEvent = (event: Event) => {
        setStatus(event.type === "load" ? "ready" : "error");
      };
      // Add event listeners
      script.addEventListener("load", setStateFromEvent);
      script.addEventListener("error", setStateFromEvent);
      // Remove event listeners on cleanup
      return () => {
        if (script) {
          script.removeEventListener("load", setStateFromEvent);
          script.removeEventListener("error", setStateFromEvent);
        }
      };
    },
    [src] // Only re-run effect if script src changes
  );

  return status;
}

export default useScript;
