/**
 * `useCrawlerDetector` is a hook that can detect whether a given `userAgent` is
 * likely to be a bot crawler. It returns a boolean `isCrawler` and a
 * `getMatches` function which you can use to detect a given agent manually.
 *
 * It's primary use is to guard expensive calculations or API operations against
 * bot traffic or misuse.
 *
 * Example
 *
 * ```
 * const ExpensiveComponent = () => {
 *   const { isCrawler } = useCrawlerDetector();
 *   const [state, setState] = useState();
 *
 *   useEffect(() => {
 *     if (isCrawler) {
 *       return;
 *     }
 *     // Perform expensive API call to setState
 *   }, [isCrawler]);
 *
 *   return (
 *     {rendered state content}
 *   );
 * };
 * ```
 */
import Crawler from "es6-crawler-detect";
import { useEffect, useMemo, useState } from "react";

const useCrawlerDetector = () => {
  const [crawlerDetector] = useState(new Crawler.Crawler());
  const [userAgent, setUserAgent] = useState("");

  useEffect(() => {
    setUserAgent(window.navigator.userAgent);
  }, []);

  const isCrawler = useMemo(
    () => crawlerDetector.isCrawler(userAgent),
    [crawlerDetector, userAgent],
  );

  return { isCrawler, getMatches: crawlerDetector.getMatches };
};

export default useCrawlerDetector;
