import {
  Box,
  Dialog,
  DialogProps,
  DialogTitle,
  Paper,
  Slide,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { TransitionProps } from "@mui/material/transitions";
import { ArrowLeftSmallIcon } from "assets/icons";
import { Button } from "comp-lib/Button";
import { Typography } from "comp-lib/Typography";
import React, { ReactNode, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import useStyles from "./FullscreenTakeover.styles";

export const useTransitionDuration = (): number => {
  const theme = useTheme();
  const isMdUp = useMediaQuery(theme.breakpoints.up("md"));
  return isMdUp ? 600 : 300;
};

export type FullScreenTakeOverProps = DialogProps & {
  headerTitle: string;
  headerImg?: string;
  backButtonTitle: string;
  onBackButtonClick?: () => void;
  open: boolean;
  stepHeader?: ReactNode;
};

const SlideTransition = React.forwardRef(function Transition(
  props: TransitionProps & { children: React.ReactElement },
  ref: React.Ref<unknown>,
) {
  const transitionDuration = useTransitionDuration();
  return (
    <Slide direction="left" ref={ref} {...props} timeout={transitionDuration} />
  );
});

const FullscreenTakeover = ({
  headerTitle,
  headerImg,
  backButtonTitle,
  onBackButtonClick,
  children,
  open,
  stepHeader,
  ...props
}: FullScreenTakeOverProps) => {
  const [isOpen, setIsOpen] = useState(open);
  const { ref, inView } = useInView({ threshold: 0 });
  const transitionDuration = useTransitionDuration();

  //Safari bug 10912 requires suppressing "isScrolled" while the takeover slides in:
  const [isSliding, setIsSliding] = useState(true);
  useEffect(() => {
    setIsSliding(true);
    const timeoutId = setTimeout(
      () => setIsSliding(false),
      transitionDuration + 200,
    );

    return () => clearTimeout(timeoutId);
  }, [isOpen, transitionDuration]);

  const isScrolled = !inView && !isSliding;

  const { classes } = useStyles({ isScrolled, headerImg });

  useEffect(() => {
    setIsOpen(open);
  }, [open]);

  function backButtonClick() {
    if (onBackButtonClick) {
      onBackButtonClick();
    }
  }

  function modalBody() {
    return (
      <>
        <div ref={ref} className={classes.inViewDetector} id="in-view-detector">
          &nbsp;
        </div>
        <div className={classes.topWrapper}>
          <Paper className={classes.backButtonContainer} square>
            <DialogTitle className={classes.backButtonDiaglog} id="back-button">
              <Button
                className={classes.backButton}
                variant="secondary-alt"
                borderless={true}
                startIcon={<ArrowLeftSmallIcon />}
                onClick={backButtonClick}
              >
                {backButtonTitle}
              </Button>
            </DialogTitle>
          </Paper>
          <Paper className={classes.headerContainer} square>
            <DialogTitle
              className={classes.headerTitleContainer}
              id="header-title"
            >
              <Typography
                variant="h1"
                component="span"
                className={classes.headerTitle}
              >
                {headerTitle}
              </Typography>
            </DialogTitle>
            {stepHeader && <Box className={classes.pill}>{stepHeader}</Box>}
          </Paper>
        </div>
        <Paper className={classes.childContainer} square>
          {children}
        </Paper>
      </>
    );
  }

  return (
    <Dialog
      open={isOpen}
      fullScreen
      TransitionComponent={SlideTransition}
      onClose={onBackButtonClick}
      {...props}
    >
      {modalBody()}
    </Dialog>
  );
};

export default FullscreenTakeover;
