/**
 * `useUrlState` is a hook for managing URL query params as component state. It
 * similar to `useState` except that it maintains the state in the URL by using
 * the `navigate` hook. Using this hook in any component that can control the
 * URL such as filtering or sorting allows the user to make changes and be able
 * to navigate backward and forward through their changes.
 *
 * This removes the need for any complex state management or decoding logic. Any
 * logic that relies on the entire URL search string can then just use the
 * `useLocation` hook combined with `URLSearchParams`.
 *
 * Examples
 *
 * ```
 * const FilterStatus = () => {
 *   const [filter, setFilter] = useUrlState("status");
 *   return (
 *     <button onClick={() => setFilter("pending")}>
 *       Filter Pending
 *     </button>
 *   );
 * };
 * ```
 *
 * Then you can use the URL state as below:
 *
 * ```
 * const useFilterLookup = () => {
 *   const location = useLocation();
 *   const debouncedLocation = useDebounce(location);
 *
 *   useEffect(() => {
 *     const filters = query.parse(debouncedLocation.search);
 *     // filters would look like: { status: "pending" }
 *     // use filters in API request. This will re-run any time
 *     // debouncedLocation changes
 *   }, [debouncedLocation]);
 * };
 * ```
 */

import {
  Dispatch,
  SetStateAction,
  useCallback,
  useEffect,
  useState,
} from "react";

interface StateUrl {
  hash: string;
  search: string;
}
// Be generic over the router implementation
interface NavigateFunction {
  ({ hash, search }: StateUrl): any;
}

type UseUrlOpts = {
  // Whether to display the defaultValue in the URL string
  showDefaultInUrl?: boolean;
};

type UseUrlStateResult<Data> = [Data, Dispatch<SetStateAction<Data>>];

interface ToString {
  toString: () => string;
}

const useUrlState = (
  key: string,
  defaultValue: ToString,
  navigate?: NavigateFunction,
  { showDefaultInUrl = true }: UseUrlOpts = {},
): UseUrlStateResult<ToString> => {
  const [storedValue, setStoredValue] = useState(defaultValue);

  const setValue: Dispatch<SetStateAction<ToString>> = useCallback(
    (newValue) => {
      if (typeof window === "undefined") {
        throw new Error("window is not available in server-rendered contexts");
      }

      const params = new URLSearchParams(window.location.search);
      const result =
        newValue instanceof Function ? newValue(storedValue) : newValue;
      // We want to avoid unnecessary history pushes if the value hasn't
      // changed. Otherwise this would make redundant GA pageviews and makes
      // pressing the back button work unexpectedly.
      if (result !== storedValue) {
        if (!showDefaultInUrl && result === defaultValue) {
          params.delete(key);
        } else {
          params.set(key, result.toString());
        }
        if (navigate) {
          navigate({ hash: window.location.hash, search: params.toString() });
        } else {
          window.location.search = params.toString();
        }
        setStoredValue(result);
      }
    },
    [storedValue, showDefaultInUrl, defaultValue, navigate, key],
  );

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const value = params.get(key);
    if (value) {
      setStoredValue(value);
    }
  }, [key]);

  return [storedValue, setValue];
};

export default useUrlState;
export type { UseUrlOpts, UseUrlStateResult, NavigateFunction, StateUrl };
