enum Evaluations {
  "GT" = ">",
  "LT" = "<",
  "GTE" = ">=",
  "LTE" = "<=",
  "EQUALS" = "=",
}

const compareVersionPart = (checkPart: string, agentPart: string) => {
  return checkPart > agentPart || agentPart === undefined
    ? Evaluations.GT
    : checkPart < agentPart
    ? Evaluations.LT
    : Evaluations.EQUALS;
};

const isPositiveInteger = (x: string) => {
  // http://stackoverflow.com/a/1019526/11236
  return /^\d+$/.test(x);
};

const validateVersions = (checkVersion: string, agentVersion: string) => {
  // split off + for build metadata
  // split off - for prerelease info
  // split by decimals to get major/minor/patch strings
  const allVersionParts = [
    ...checkVersion.split("+")[0].split("-")[0].split("."),
    ...agentVersion.split("+")[0].split("-")[0].split("."),
  ];
  const allPartsArePositiveIntegers = allVersionParts.every((part) =>
    isPositiveInteger(part),
  );

  return allPartsArePositiveIntegers;
};
/**
 * ]::::][======> CAUTION <======][::::[
 *
 * @description Before using this util, please confirm your use case with architecture.
 * This util is not guaranteed to work in all cases and should be avoided if at all possible.
 *
 * Compare two software version numbers (e.g. 1.7.1)
 * @Returns
 *  "<" if checkVersion is lesser, ">" if checkVersion is greater, "=" if equal, NaN if wrong format
 */
const compareVersionStrings = (checkVersion: string, agentVersion: string) => {
  // First, validate both numbers are true version numbers
  const areVersionsValid = validateVersions(checkVersion, agentVersion);
  if (!areVersionsValid) {
    return NaN;
  }

  // split off metadata or pre-release info from version strings
  // default all values to 0, so 1.4 turns into 1.4.0, which allows
  // us to make stable comparisons by ensuring both versions have 3 values to compare.
  const [checkMajor = "0", checkMinor = "0", checkPatch = "0"] = checkVersion
    .split("+")[0]
    .split("-")[0]
    .split(".");
  const [agentMajor = "0", agentMinor = "0", agentPatch = "0"] = agentVersion
    .split("+")[0]
    .split("-")[0]
    .split(".");

  const majorVersionComparison = compareVersionPart(checkMajor, agentMajor);

  // if major versions aren't the same, return evaluation, otherwise compare minor versions
  if (majorVersionComparison !== Evaluations.EQUALS) {
    return majorVersionComparison;
  } else {
    const minorVersionComparison = compareVersionPart(checkMinor, agentMinor);

    // if minor versions aren't the same, return evaluation, otherwise return evaluation on patch versions
    if (minorVersionComparison !== Evaluations.EQUALS) {
      return minorVersionComparison;
    } else {
      return compareVersionPart(checkPatch, agentPatch);
    }
  }
};

export { validateVersions, compareVersionStrings, Evaluations };
