import {
  Fade,
  Menu,
  MenuClassKey,
  MenuItem,
  PopoverOrigin,
  StyledComponentProps,
} from "@mui/material";
import React, { useEffect } from "react";
import useStyles from "./DropdownMenu.styles";

export type MenuOption = {
  id?: string;
  title: string;
  a11yLabel?: string;
  disabled?: boolean;
  component?: JSX.Element;
  componentOnly?: boolean;
  classes?: string;
};

type DropdownMenuProps = React.HTMLAttributes<HTMLDivElement> &
  StyledComponentProps<MenuClassKey> & {
    anchorEl: HTMLElement | null;
    anchorOrigin?: PopoverOrigin;
    isOpen: boolean;
    menuOptions: MenuOption[];
    onClose: () => void;
    onSelection: (index: number, item?: any) => void;
    transformOrigin?: PopoverOrigin;
    selectedIndex?: number;
    elevation?: number;
  };

const DropdownMenu = ({
  anchorOrigin = {
    horizontal: "right",
    vertical: "bottom",
  },
  classes,
  isOpen,
  menuOptions,
  onClose,
  onSelection,
  transformOrigin = {
    horizontal: "right",
    vertical: "top",
  },
  selectedIndex,
  elevation = 4,
  ...menuProps
}: DropdownMenuProps) => {
  const { classes: menuClasses, cx } = useStyles();
  const handleSelection = (selectedIndex: number, item: any) => {
    onClose();
    onSelection(selectedIndex, item);
  };

  useEffect(() => {
    menuOptions.forEach((option) => {
      if (!option.componentOnly && option.classes) {
        throw new Error(
          `Menu option "${option.title}" should only provide a classes argument if the componentOnly argument is set to true.`,
        );
      }
    });
  }, [menuOptions]);

  return (
    <Menu
      anchorOrigin={anchorOrigin}
      classes={{
        ...classes,
        paper: cx(menuClasses.paper),
        list: cx(menuClasses.menuList, classes?.list),
      }}
      elevation={elevation}
      onClose={onClose}
      open={isOpen}
      transformOrigin={transformOrigin}
      variant="menu"
      TransitionComponent={Fade}
      {...menuProps}
    >
      {menuOptions.map((option, index) => {
        return option.componentOnly ? (
          <li
            id={option.id}
            aria-label={option.a11yLabel || option.title}
            key={option.title}
            tabIndex={option.disabled ? undefined : -1}
            role="menuitem"
            onKeyUp={(e) => {
              if (e.key === "Enter") {
                handleSelection(index, option);
              }
            }}
            className={option.classes}
          >
            {option.component}
          </li>
        ) : (
          <MenuItem
            id={option.id}
            aria-label={option.a11yLabel || option.title}
            disabled={option.disabled}
            className={menuClasses.menuItem}
            key={option.title}
            selected={index === selectedIndex}
            onClick={() => handleSelection(index, option)}
          >
            {option.component ? option.component : option.title}
          </MenuItem>
        );
      })}
    </Menu>
  );
};

export default DropdownMenu;
