import { useEffect, useRef, useState } from "react";
import type { Animation, AnimationData, AnimationSegment } from "./index";
import { SpriteSegments, useSpriteAnimation } from "./SpriteAnimation.styles";

// Sprite animation is accomplished by compiling all of your animation frames
// into a single strip, just like a strip of film. That strip is then
// played exactly the same as a movie playing through a film projector: the
// sprite strip is moved behind a viewing port one frame at a time.
//
// createAnimation() calculates all of the data needed to move the sprite strip
// using CSS transformations. It can calculate all of the necessary position
// data for any number of sub-sections (segments) of the strip, defined by
// start and end frames.
//
// CSS for the transformations is created in SpriteAnimation.styles.ts which
// uses the data calculated here to generate CSS animations which transform
// the translateX position of the strip. Translate transformations get hardware
// acceleration! The transformation is done using the step timing function to
// move the strip one step per frame rather than continuously. This results in
// a frame based animation rather than a sliding animation.
//
// Finally, these transformations can be applied to the sprite strip by adding
// and removing CSS classes or by calling the element.animate api. That is done
// in a useEffect of the SpriteAnimation component, which detects changes to
// the chosen animation segment and applies the appropriate CSS classes.

const createAnimation = (data: AnimationData) => {
  const a: Animation = {
    data,
    fps: data.totalSeconds / data.totalFrames,
    segments: {},
  };

  //calculate position data for each segment, assuming that we need to
  // animate the sprite strip from right to left using translateX.
  // The left side of the first frame is at x = 0 * frame_width
  // The left side of the third frame is at x = 2 * frame_width
  for (const segName in data.segments) {
    const segData = data.segments[segName];
    const segment: AnimationSegment = {};

    //total number of frames in this segment
    segment.framesToPlay = segData.endFrame - segData.startFrame;

    //the width of the strip to ignore before the start of this segment
    segment.startOffsetWidth = segData.startFrame * a.data.frameWidth;

    //total width of this segment
    segment.animationWidth = segment.framesToPlay * a.data.frameWidth;

    //total duration of this segment in seconds (calculated if not specified)
    segment.durationSEC = segData.duration
      ? segData.duration / 1000
      : a.fps * segment.framesToPlay;

    //the left edge of the last frame in this segment in pixels
    segment.finalFramePX =
      -1 * (segment.startOffsetWidth + segment.animationWidth);

    //the left edge of the first frame in this segment in pixels
    segment.startOffsetPX = -1 * segment.startOffsetWidth;

    //passthrough props for dom animation
    //https://developer.mozilla.org/en-US/docs/Web/API/KeyframeEffect/KeyframeEffect#parameters
    segment.iterations = segData.iterations;
    segment.direction = segData.direction;
    segment.delay = segData.delay;

    a.segments[segName] = segment;
  }

  return a;
};

//Animation segments are contained in an indexed property of the Animation type:
//  type Animation {
//    ...
//    segments: { [key: string]: AnimationSegment }
//  }
//This function is to help validate that we have a valid segment name (key) when
// chosing which animation segment should play
const validateSegment = (
  segments: SpriteSegments,
  segmentName: string,
): void => {
  const segmentNames = Object.keys(segments);
  if (!segmentNames.includes(segmentName)) {
    throw new Error(`Segment ${segmentName} does not exist! 
        Valid segments are ${segmentNames.join(",")}`);
  }
};

export type SpriteAnimationProps = {
  animation: Animation;
  segmentToPlay: string;
  animate?: boolean;
};

const SpriteAnimation = ({
  animation,
  segmentToPlay,
  animate = true,
  ...props
}: SpriteAnimationProps) => {
  const [curSeg, setCurSeg] = useState<string>();
  const segments = useSpriteAnimation(animation);
  const img = useRef<HTMLImageElement>(null);

  //this validation exists outside of useEffect to provide meaningful
  // error output when this component has an invalid initial value
  // in segmentToPlay. Without this validation, you get a difficult to
  // diagnose react rendering failure.
  validateSegment(segments, segmentToPlay);

  useEffect(() => {
    if (segmentToPlay !== curSeg) {
      validateSegment(segments, segmentToPlay);
      setCurSeg(segmentToPlay);

      const segment = animation.segments[segmentToPlay];

      try {
        if (animation.data.debug) {
          console.log(segmentToPlay, segment);
        }

        //kill and remove any running animations
        img.current?.getAnimations().forEach((a) => a.cancel());

        //start a new animation (segmentToPlay has changed!)
        img.current
          ?.animate(
            {
              transform: [
                `translateX(${segment.startOffsetPX}px)`,
                `translateX(${segment.finalFramePX}px)`,
              ],
            },
            {
              easing: `steps(${segment.framesToPlay})`,
              duration: animate ? (segment.durationSEC ?? 0) * 1000 : 0,
              iterations: segment.iterations ?? 1,
              direction: segment.direction ?? "normal",
              delay: segment.delay,
            },
          )
          .finished?.catch((error: DOMException) => {
            if (error.name !== "AbortError") {
              throw error;
            }
          })
          .finally(() => {
            if (img.current) {
              img.current.style.transform = `translateX(${segment.finalFramePX}px)`;
            }
          });
      } catch (e) {
        //handle non-compliant browsers (and jest) that don't implement the animation api
        //https://caniuse.com/mdn-api_element_animate
        if (img?.current?.style) {
          img.current.style.transform = `translateX(${segment.finalFramePX}px)`;
        }
      }
    }
  }, [
    curSeg,
    animation.segments,
    animation.data.debug,
    segments,
    segmentToPlay,
    animate,
  ]);

  return (
    <div className={segments[segmentToPlay].spriteContainer} {...props}>
      <img
        src={animation.data.spritePath}
        alt=""
        ref={img}
        className={segments[segmentToPlay].sprite}
      />
    </div>
  );
};

export default SpriteAnimation;
export { createAnimation };
