import { FormHelperText } from "@mui/material";
import { SearchIcon } from "assets/icons";
import { IconButton } from "comp-lib/IconButton";
import type { TextFieldProps } from "comp-lib/TextField";
import { TextField } from "comp-lib/TextField";
import { KeyboardEvent } from "react";
import useStyles from "./SearchBar.styles";

type SearchBarProps = Omit<TextFieldProps, "hasOptionalText"> & {
  onClick: () => void;
  isLoading?: boolean;
  id: string;
};

const SearchBar = ({
  error,
  helperText,
  isLoading,
  onClick,
  InputLabelProps,
  ...rest
}: SearchBarProps) => {
  const { classes, cx } = useStyles();

  return (
    <>
      <div className={classes.container}>
        <TextField
          className={classes.input}
          error={error}
          InputLabelProps={{
            className: cx(
              classes.label,
              InputLabelProps && InputLabelProps.className,
            ),
            ...InputLabelProps,
          }}
          onKeyDown={(event: KeyboardEvent<HTMLInputElement>) => {
            if (event.key === "Enter") {
              onClick();
            }
          }}
          {...rest}
        />
        <IconButton
          aria-label="search"
          className={classes.searchButton}
          isLoading={isLoading}
          onClick={onClick}
          size="large"
        >
          <SearchIcon />
        </IconButton>
      </div>
      {helperText && (
        <FormHelperText error={error} className={classes.helperText}>
          {helperText}
        </FormHelperText>
      )}
    </>
  );
};

export default SearchBar;
export type { SearchBarProps };
