import { useMediaQuery } from "@mui/material";
import { useTheme } from "@mui/material/styles";

const useBreakpoint = () => {
  const theme = useTheme();
  const xsDown: boolean = useMediaQuery(theme.breakpoints.down("sm"));
  const xsUp: boolean = useMediaQuery(theme.breakpoints.up("xs"));
  const smDown: boolean = useMediaQuery(theme.breakpoints.down("md"));
  const smUp: boolean = useMediaQuery(theme.breakpoints.up("sm"));
  const mdDown: boolean = useMediaQuery(theme.breakpoints.down("lg"));
  const mdUp: boolean = useMediaQuery(theme.breakpoints.up("md"));
  const mdToLg: boolean = useMediaQuery(theme.breakpoints.between("md", "xl"));
  const lgDown: boolean = useMediaQuery(theme.breakpoints.down("xl"));
  const lgUp: boolean = useMediaQuery(theme.breakpoints.up("lg"));
  const xlDown: boolean = useMediaQuery(theme.breakpoints.down("xxl"));
  const xlUp: boolean = useMediaQuery(theme.breakpoints.up("xl"));
  const xxlDown: boolean = useMediaQuery(theme.breakpoints.down("xxl"));
  const xxlUp: boolean = useMediaQuery(theme.breakpoints.up("xxl"));

  return {
    xsDown,
    xsUp,
    smDown,
    smUp,
    mdDown,
    mdUp,
    mdToLg,
    lgDown,
    lgUp,
    xlDown,
    xlUp,
    xxlDown,
    xxlUp,
  };
};

export default useBreakpoint;
