import { makeStyles } from "tss-react/mui";

const TOUCH_TARGET_LENGTH = 44;

const getTouchTarget = (buttonLength: number, disableTouchTarget: boolean) => {
  const lengthDiff = (buttonLength - TOUCH_TARGET_LENGTH) / 2;
  if (lengthDiff <= 0 && !disableTouchTarget) {
    return {
      "&::after": {
        position: "absolute" as "absolute",
        content: "''",
        left: lengthDiff,
        top: lengthDiff,
        width: TOUCH_TARGET_LENGTH,
        height: TOUCH_TARGET_LENGTH,
        border: "none",
      },
    };
  }
};

type iconButtonStyleProps = {
  buttonLength: number;
  borderRadius: number;
  disableTouchTarget: boolean;
  svgSize: number;
};

const useStyles = makeStyles<iconButtonStyleProps>({ name: "MuiDWIconBtn" })(
  (theme, { buttonLength, borderRadius, disableTouchTarget, svgSize }) => ({
    iconButton: {
      [theme.breakpoints.up("_")]: {
        height: buttonLength,
        width: buttonLength,
        minHeight: 0,
        minWidth: 0,
        padding: 0,
        borderRadius: borderRadius,
        "&:after": {
          borderRadius: borderRadius,
        },
        "&.Mui-focusVisible:before": {
          borderRadius: borderRadius,
        },
      },
    },
    iconContainer: {
      "& svg": {
        height: svgSize,
        width: svgSize,
      },
      ...getTouchTarget(buttonLength, disableTouchTarget),
    },
  }),
);

export default useStyles;
