/**
 * `useLoading` is a simple timeout hook that helps ensure loading state
 * resolves to a completed value in a reasonable time. It behaves similar to a
 * `useState` hook with a true/false value for loading or not. It takes the
 * initial loading state, a timeout, and what to do when the timeout is reached.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const [error, setError] = useState();
 *
 *   const handleTimeout = useCallback(() => setError("Timed out"));
 *
 *   // Default timeout is 5 seconds
 *   const [isLoading, setIsLoading] = useLoading(false, handleTimeout);
 *   const [results, setResults] = useState([]);
 *
 *   useEffect(() => {
 *     if (isLoading) {
 *       return;
 *     }
 *     setIsLoading(true);
 *     // If this takes longer than 5 seconds, an error will be displayed
 *     getResults()
 *       .then((results) => setResults(results))
 *       .finally(() => setIsLoading(false));
 *   }, [isLoading]);
 *
 *   return (
 *     <div>
 *       {error ? <h1>{error}</h2> : null}
 *       {results.map((value) => (
 *         <h4 key={value.id}>{value.title}</h4>
 *       ))}
 *     </div>
 *   );
 * };
 * ```
 */
import { useCallback, useEffect, useRef, useState } from "react";

const defaultTimeout = 5000;

const useLoading = (
  initialState: boolean,
  onTimeout: () => void,
  timeout: number = defaultTimeout
): [boolean, (loading: boolean) => void] => {
  const [isLoading, setIsLoading] = useState(initialState);
  const timerRef = useRef<ReturnType<typeof setTimeout>>();

  useEffect(() => {
    if (!isLoading) {
      return;
    }
    timerRef.current = setTimeout(() => {
      if (isLoading) {
        onTimeout();
      }
    }, timeout);
    return () => {
      timerRef.current && clearTimeout(timerRef.current);
    };
  }, [isLoading, onTimeout, timeout]);

  const setLoading = useCallback((loading: boolean) => {
    if (!loading && timerRef.current) {
      clearTimeout(timerRef.current);
    }
    setIsLoading(loading);
  }, []);

  return [isLoading, setLoading];
};

export default useLoading;
export { defaultTimeout };
