/**
 * `useCookie` provides a simple wrapper around the `document.cookie`
 * protocol. It behaves similar to `useState` except that it maintains the state
 * in a cookie with an expiration and SameSite attribute.
 *
 * Example
 *
 * ```
 * const UserProfile = () => {
 *   // cacheDays defaults to 30
 *   const [lastVisited, setLastVisited] = useCookie("lastVisited", Date.now(), {
 *     sameSite: "strict",
 *   });
 *
 *   useEffect(() => {
 *     setLastVisited(Date.now());
 *   });
 *
 *   return lastVisited > 0 ? <h1>Welcome, back!</h1> : <h1>Welcome!</h1>;
 * };
 * ```
 */

import { Dispatch, SetStateAction, useCallback, useState } from "react";

type SameSite = "lax" | "strict" | "none";
type CookieOpts = {
  expireDays?: number;
  domain?: string;
  path?: string;
  secure?: boolean;
  sameSite?: SameSite;
};
type CookieResult<Data> = [Data, Dispatch<SetStateAction<Data>>];

const defaultExpireDays = 30;
const defaultPath = "/";
const defaultSameSite: SameSite = "strict";

const retrieveCookie = <Data>(key: string): null | Data => {
  if (typeof document === "undefined") {
    return null;
  }
  return document.cookie
    .split(";")
    .reduce<null | Data>((result, currentCookie) => {
      const [storedKey, storedValue] = currentCookie.trim().split("=");
      try {
        return key === storedKey
          ? JSON.parse(decodeURIComponent(storedValue))
          : result;
      } catch (err: unknown) {
        if (err instanceof Error) {
          console.error(err.message);
        }
        return result;
      }
    }, null);
};

const storeCookie = <Data>(
  key: string,
  value: Data,
  {
    expireDays = defaultExpireDays,
    domain,
    secure = true,
    sameSite = defaultSameSite,
    path = defaultPath,
  }: CookieOpts = {},
) => {
  if (typeof document === "undefined") {
    throw new Error("document is not available in server-rendered contexts");
  }
  const valueString = typeof value === "string" ? value : JSON.stringify(value);
  const options = [];
  if (expireDays) {
    const expires = new Date();
    expires.setDate(expires.getDate() + expireDays);
    options.push(`expires=${expires.toUTCString()}`);
  }
  options.push(secure && "secure");
  options.push(sameSite && `SameSite=${sameSite}`);
  options.push(domain && `domain=${domain}`);
  options.push(path && `path=${path}`);

  const cookie = `${key}=${valueString}; ${options.filter((o) => o).join(";")}`;
  document.cookie = cookie;
  return cookie;
};

const clearCookie = (key: string, path: string = defaultPath) => {
  if (typeof document === "undefined") {
    throw new Error("document is not available in server-rendered contexts");
  }
  const expires = new Date();
  expires.setDate(expires.getDate() - 1);
  const cookie = `${key}=''; expires=${expires.toUTCString()}; path=${path}`;
  document.cookie = cookie;
};

const useCookie = <Data>(
  key: string,
  initialValue: Data,
  opts?: CookieOpts,
): CookieResult<Data> => {
  const [storedValue, setStoredValue] = useState<Data>(
    () => retrieveCookie(key) ?? initialValue,
  );

  const setValue: Dispatch<SetStateAction<Data>> = useCallback(
    (newValue) => {
      setStoredValue((currentValue) => {
        const result =
          newValue instanceof Function ? newValue(currentValue) : newValue;
        if (typeof document !== "undefined") {
          result
            ? storeCookie(key, JSON.stringify(result), opts)
            : clearCookie(key);
        }
        return result;
      });
    },
    [key, opts],
  );

  return [storedValue, setValue];
};

export default useCookie;
export { storeCookie, retrieveCookie, clearCookie };
export type { SameSite, CookieOpts };
