import { AlertTypes } from "comp-lib/models/alert-types";
import { makeStyles } from "tss-react/mui";
import { AlertColors } from "./toastTypes";

type ToastStyleProps = {
  alertType: AlertTypes;
  anchorOrigin?: {
    horizontal: "center" | "left" | "right";
    vertical: "bottom" | "top";
  };
};

const getHeaderMargin = (topAligned: boolean) => {
  if (topAligned && typeof document !== "undefined") {
    const header = document.getElementsByTagName("header")[0];
    if (header) {
      const marginTop = header.getBoundingClientRect().bottom;
      return {
        marginTop,
      };
    }
  }
};

const useStyles = makeStyles<ToastStyleProps>({
  name: "MuiDWToast",
})((theme, { anchorOrigin, alertType }) => ({
  snackbar: {
    ...getHeaderMargin(anchorOrigin?.vertical === "top"),
    maxWidth: 366,
    zIndex: 1200, // in case getHeaderMargin is incorrect and AppBar overlaps
    borderRadius: 8,
    [theme.breakpoints.only("xs")]: {
      maxWidth: 259,
    },
  },
  snackbarWrapper: {
    zIndex: 0,
    border: `1px solid ${theme.colors.neutral[200]}`,
    borderRadius: 8,
  },
  snackBarBorder: {
    border: `1px solid ${theme.colors.neutral[200]}`,
    borderRadius: 8,
  },
  snackbarContent: {
    position: "relative",
    backgroundColor: theme.colors.neutral[0],
    color: theme.colors.neutral[800],
    boxShadow: theme.shadows[1],
    borderRadius: 8,
    borderLeftStyle: "solid",
    borderLeftWidth: 8,
    borderColor: AlertColors(theme)[alertType],
    flexWrap: "nowrap",
    justifyContent: "center",
    minHeight: 60,
    padding: theme.spacing(2, 2, 2, 2),
    zIndex: 0,
    "&:hover": {
      boxShadow: theme.shadows[24],
    },
    "& button, button:active": {
      backgroundColor: theme.colors.neutral[0],
      paddingTop: theme.spacing(1),
      "&:hover": {
        backgroundColor: theme.colors.neutral[0],
      },
    },
    "& .MuiSnackbarContent-action": {
      paddingLeft: theme.spacing(1),
    },
    "& .MuiSnackbarContent-message": {
      padding: theme.spacing(0, 3.75, 0, 5.625),
    },
  },
  icon: {
    position: "absolute",
    left: 20,
    top: 0,
    width: 28,
    height: 28,
    marginTop: 15,
    color: AlertColors(theme)[alertType],
  },
  iconButton: {
    position: "absolute",
    top: 0,
    right: 5,
    marginTop: 3,
  },
  fadeIn: {
    zIndex: 0,
  },
}));

export default useStyles;
