import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuProps,
  Select as MUISelect,
  SelectProps as MUISelectProps,
  TextFieldProps,
} from "@mui/material";
import {
  CheckCircleIcon,
  ChevronDownIcon,
  CloseCircleIcon,
} from "assets/icons";
import React from "react";
import useStyles from "./Select.styles";

export type SelectProps = Omit<MUISelectProps, "variant"> &
  Pick<TextFieldProps, "error" | "helperText" | "InputLabelProps"> &
  Required<Pick<TextFieldProps, "id">> & {
    valid?: boolean;
  };

const Select = ({
  className,
  children,
  displayEmpty = true,
  fullWidth,
  helperText,
  IconComponent,
  id,
  InputLabelProps,
  error,
  label,
  MenuProps,
  onChange,
  placeholder,
  required,
  SelectDisplayProps,
  valid,
  value,
  ...rest
}: SelectProps) => {
  const { classes, cx } = useStyles();

  const menuProps: Partial<MenuProps> = {
    anchorOrigin: {
      vertical: "bottom" as const,
      horizontal: "left" as const,
    },
    transformOrigin: {
      vertical: "top" as const,
      horizontal: "left" as const,
    },
    ...MenuProps,
    classes: {
      root: cx(classes.menuRoot, MenuProps?.classes?.root),
      paper: cx(classes.paper, MenuProps?.classes?.paper),
      list: cx(classes.menuList, MenuProps?.classes?.list),
    },
  };

  const isPlaceholder = value === "";

  const renderWithIcon = () => {
    let displayValue;
    const childrenArray = React.Children.toArray(children);

    // Use find to iterate through the array until the value from props
    // matches the value of the item. When the item matches set `displayValue`
    // to the children of the MenuItem. This is needed for now becuase the
    // `renderValue` prop on Select only has access to the value of the MenuItem
    // and not the content of the MenuItem.
    //
    // See open issue:
    // https://github.com/mui-org/material-ui/issues/19585
    childrenArray.find((child) => {
      if (!React.isValidElement(child)) {
        return null;
      }
      if (child.props.value === value) {
        displayValue = child.props.children;
      }
      return false;
    });

    return (
      <span className={classes.selectDisplayWrapper}>
        <span className={classes.selectText}>
          {value !== "" ? displayValue : placeholder}
        </span>
        {valid && (
          <CheckCircleIcon
            aria-hidden="true"
            className={cx(classes.selectIcon, classes.selectIconValid)}
          />
        )}
        {error && (
          <CloseCircleIcon
            aria-hidden="true"
            className={cx(classes.selectIcon, classes.selectIconError)}
          />
        )}
      </span>
    );
  };

  const iconComponent = ({ className }: { className: string }) => {
    return <ChevronDownIcon className={cx(className, classes.arrowIcon)} />;
  };

  return (
    <FormControl
      className={cx(classes.formControl, className)}
      fullWidth={fullWidth}
      required={required}
    >
      <InputLabel
        id={`${id}-label`}
        {...InputLabelProps}
        className={cx(InputLabelProps?.className, classes.label)}
        classes={{
          asterisk: cx(classes.required, InputLabelProps?.classes?.asterisk),
          ...InputLabelProps?.classes,
        }}
        shrink={true} // setting this to true, to match textfield.
        // Note that setting to false here does not appear to hide the placeholder text, as it does in Textfield
      >
        {label}
      </InputLabel>
      <MUISelect
        classes={{
          select: cx([
            classes.select,
            valid && classes.selectValid,
            error && classes.selectError,
            isPlaceholder && classes.selectPlaceholder,
          ]),
        }}
        displayEmpty={displayEmpty}
        IconComponent={IconComponent || iconComponent}
        id={id}
        labelId={`${id}-label`}
        MenuProps={menuProps}
        renderValue={renderWithIcon}
        SelectDisplayProps={{
          "aria-describedby": `${id}-helper-text`,
          ...SelectDisplayProps,
        }}
        required={required}
        value={value}
        onChange={onChange}
        {...rest}
      >
        {children}
      </MUISelect>
      {helperText ? (
        <FormHelperText
          id={`${id}-helper-text`}
          error={error}
          className={classes.helperText}
        >
          {helperText}
        </FormHelperText>
      ) : null}
    </FormControl>
  );
};

export default Select;
