import { Components } from "@mui/material";
import { Colors } from "./colors";

const pxToRem = (size: number) => `${size / 14}rem`;

export const createOverrides = ({
  breakpoints,
  _typography,
}: {
  breakpoints: any;
  _typography: any;
}): Partial<Components> => {
  const overrides: Partial<Components> = {
    MuiAppBar: {
      defaultProps: {
        color: "transparent",
      },
    },
    MuiCssBaseline: {
      styleOverrides: {
        "@global": {
          body: {
            "& #cnpoke": {
              zIndex: "10 !important",
            },
            color: Colors.neutral[800],
            fontSize: pxToRem(16),
            lineHeight: "1.43",
          },
          ".js-focus-visible :focus:not(.focus-visible)": {
            outline: 0,
          },
          // Screen reader only
          ".sr-only": {
            position: "absolute",
            left: -10000,
            top: "auto",
            width: 1,
            height: 1,
            overflow: "hidden",
          },
          a: {
            textUnderlineOffset: "5px",
            color: Colors.primary[500],
            textDecorationColor: Colors.primary[300],
            textDecorationThickness: "1px",
            fontWeight: 600,
            "&:focus ": {
              outline: "none",
            },
            "&.focus-visible": {
              position: "relative",
            },
            "&.focus-visible:before": {
              content: `" "`,
              position: "absolute",
              top: 0,
              left: 0,
              width: "100%",
              height: "80%",
              padding: "4px",
              boxShadow: `0 0 0 4px ${Colors.primary[300]}`,
              borderRadius: "1px",
              boxSizing: "content-box",
              transform: "translate(-4px, -2px)",
            },
            "&:active:before": {
              boxShadow: "none ",
            },
            "&:hover": {
              color: Colors.primary[700],
              textDecorationColor: Colors.primary[400],
            },
            "&:active": {
              color: Colors.primary[900],
              textDecorationColor: Colors.primary[500],
            },
          },
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          minHeight: "32px",
          minWidth: "44px",
          textTransform: "none",
        },
      },
    },
    MuiButtonBase: {
      defaultProps: {
        disableRipple: true,
      },
      styleOverrides: {
        root: {
          minHeight: "32px",
          minWidth: "44px",
          "&:focus": {
            outline: "none",
          },
          "&.Mui-focusVisible:before": {
            content: `" "`,
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            padding: 0,
            transform: "none",
            borderRadius: "50px",
            boxShadow: `0 0 0 4px ${Colors.primary[300]}`,
            boxSizing: "content-box",
          },
        },
      },
    },
    MuiDialog: {
      styleOverrides: {
        root: {
          "& .MuiBackdrop-root": {
            backgroundColor: "rgb(53, 53, 53, .5)",
          },
        },
        paperFullWidth: {
          width: "calc(100% - 32px)",
        },
      },
    },
    MuiModal: {
      styleOverrides: {
        root: {
          "& .MuiBackdrop-root": {
            backgroundColor: "rgb(53, 53, 53, .5)",
          },
        },
      },
    },
    MuiIconButton: {
      styleOverrides: {
        root: {
          borderRadius: 4,
        },
      },
    },
    MuiInputLabel: {
      styleOverrides: {
        formControl: {
          position: "static", // note that setting this to static makes the "shrink" functionality no longer work
        },
      },
    },
    MuiInputBase: {
      styleOverrides: {
        formControl: {
          "label + &": {
            marginTop: 4,
          },
        },
      },
    },
    MuiMenuItem: {
      defaultProps: {
        disableGutters: true,
      },
    },
    MuiSelect: {
      styleOverrides: {
        icon: {
          top: "calc(50% - .75em)", // 50% - 12px
        },
      },
    },
    MuiTab: {
      defaultProps: {
        disableFocusRipple: true,
        disableRipple: true,
      },
    },
    MuiTabs: {
      defaultProps: {
        indicatorColor: "primary",
        selectionFollowsFocus: true,
      },
    },
    MuiToolbar: {
      defaultProps: {
        disableGutters: true,
        variant: "dense",
      },
      styleOverrides: {
        root: {
          padding: "0 10px",
          [breakpoints.up("md")]: {
            padding: "0 40px",
          },
          [breakpoints.up("xl")]: {
            padding: "0 72px",
          },
        },
        dense: {
          minHeight: 57,
          [breakpoints.up("md")]: {
            minHeight: 61,
          },
          [breakpoints.up("xl")]: {
            minHeight: 76,
          },
        },
      },
    },
    MuiUseMediaQuery: {
      defaultProps: {
        noSsr: true,
      },
    },
  };

  return overrides;
};

export default createOverrides;
