/**
 * `useTimeZone` is a wrapper around Google's Timezone API. It accepts either a
 * partial `Address` or `Coords` object and returns several timezone properties
 * that can be used to construct the correct local time for the user.
 *
 * Requires a `<GeoProvider />` context with valid API keys.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const { getTimeZone } = useTimeZone();
 *   const [timeZone, setTimeZone] = useState("America/Los_Angeles");
 *
 *   useEffect(() => {
 *     getTimeZone({ city: "Detroit", state: "MI" })
 *       .then(({ timeZoneId }) => setTimeZone(timeZoneId));
 *   }, [getTimeZone]);
 *
 *   return <div>Detroit, MI is in the {timeZone} timezone.</div>;
 * };
 * ```
 */

import useCoords from "hooks/useCoords";
import { GeoAddress, GeoContext } from "providers/GeoProvider";
import { useCallback, useContext, useState } from "react";
import { retrieveCookie, storeCookie } from "./useCookie";
import useCrawlerDetector from "./useCrawlerDetector";

const TIMEZONE_URL = "https://maps.googleapis.com/maps/api/timezone/json";

// Used to avoid geolocating accidentally during E2E tests
const mockTimeZone: TimeZoneResult = {
  dstOffset: 0,
  localTime: Date.now() - 28800000,
  rawOffset: 0,
  timeZoneId: "America/Los_Angeles",
  timeZoneName: "Pacific Daylight Time",
};

type TimeZoneResult = {
  dstOffset: number;
  localTime: number;
  rawOffset: number;
  timeZoneId: string;
  timeZoneName: string;
};

const stringifyLocation = (location: GeoAddress): string => {
  const { postalCode, lat, lng } = location;
  return [postalCode, lat, lng].join("-");
};

const useTimeZone = () => {
  const { timeZoneApiKey } = useContext(GeoContext);
  const [timeZone, setTimeZone] = useState<null | TimeZoneResult>(null);
  const { getCoords } = useCoords();
  const { isCrawler } = useCrawlerDetector();

  const getTimeZone = useCallback(
    async (
      location: GeoAddress,
      timestamp: number,
    ): Promise<TimeZoneResult> => {
      try {
        if (!timeZoneApiKey) {
          throw new Error("TimeZone API Key is not defined");
        } else if (!location.lat || !location.lng) {
          const coords = await getCoords(location);
          if (coords.length > 0 && coords[0].lat && coords[0].lng) {
            location.lat = coords[0].lat;
            location.lng = coords[0].lng;
          } else {
            throw Error("No coordinate results found.");
          }
        }

        const locationStr = stringifyLocation(location);
        const cacheKey = `timeZone-${locationStr}`;
        const cache = retrieveCookie<TimeZoneResult>(cacheKey);
        if (cache) {
          setTimeZone(cache);
          return cache;
        } else if (
          (typeof window !== "undefined" &&
            window.localStorage.getItem("PW")) ||
          isCrawler
        ) {
          setTimeZone(mockTimeZone);
          return mockTimeZone;
        }

        const requestUrl = new URL(TIMEZONE_URL);
        requestUrl.searchParams.set("key", timeZoneApiKey);
        requestUrl.searchParams.set(
          "location",
          [location.lat, location.lng].join(","),
        );
        requestUrl.searchParams.set("timestamp", (timestamp / 1000).toString());

        const response = await fetch(requestUrl.toString());
        const {
          status,
          errorMessage,
          dstOffset,
          rawOffset,
          timeZoneId,
          timeZoneName,
        } = await response.json();

        if (status === "OK") {
          const result = {
            timeZoneId,
            timeZoneName,
            dstOffset,
            rawOffset,
            localTime: (timestamp / 1000 + dstOffset + rawOffset) * 1000,
          };
          setTimeZone(result);
          storeCookie(cacheKey, result);
          return result;
        } else {
          throw new Error(
            `Timezone lookup failed due to: ${status}. Message: ${errorMessage}`,
          );
        }
      } catch (err: unknown) {
        if (err instanceof Error) {
          console.error(err.message);
        }
        throw err;
      }
    },
    [getCoords, isCrawler, timeZoneApiKey],
  );

  return { timeZone, getTimeZone };
};

export default useTimeZone;
export type { TimeZoneResult };
export { mockTimeZone, stringifyLocation };
