import FormControl from "@mui/material/FormControl";
import FormGroup from "@mui/material/FormGroup";
import SimpleCheckbox from "comp-lib/Checkbox/SimpleCheckbox";
import React from "react";
import useStyles from "./SimpleCheckboxGroup.styles";

export type SimpleCheckboxOption = {
  label: string;
  checked: boolean;
};

type SimpleCheckboxGroupProps = {
  options: SimpleCheckboxOption[];
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  groupLabel?: string;
  hintText?: string;
  error?: boolean;
  id?: string;
};

const SimpleCheckboxGroup = ({
  options,
  onChange,
  error,
  groupLabel,
  hintText,
  id,
}: SimpleCheckboxGroupProps) => {
  const { classes, cx } = useStyles();

  return (
    <>
      <FormControl className={classes.root} component="fieldset" error={error}>
        <div>
          <div className={classes.groupLabelContainer}>
            <div className={classes.groupLabel}>{groupLabel}</div>
          </div>
          {error ? (
            <div className={classes.hintError}>{hintText}</div>
          ) : (
            <div className={classes.hintLabel}>{hintText}</div>
          )}
        </div>
        <FormGroup
          id={id ?? "simpleCheckboxGroup"}
          classes={{
            root: cx(classes.formGroup, {
              [classes.formGroupError]: error,
            }),
          }}
        >
          {options.map((option: SimpleCheckboxOption, index: number) => {
            return (
              <SimpleCheckbox
                key={`${option.label}-checkbox`}
                label={option.label}
                checkboxProps={{
                  checked: option.checked,
                  onChange: onChange,
                  name: option.label,
                  id: (id ?? "simpleCheckboxGroup") + "_option_" + index,
                }}
              />
            );
          })}
        </FormGroup>
      </FormControl>
    </>
  );
};

export default SimpleCheckboxGroup;
