import FormControlLabel from "@mui/material/FormControlLabel";
import Radio, { RadioProps as MuiRadioProps } from "@mui/material/Radio";
import { Typography } from "comp-lib/Typography";
import { useCallback, useEffect, useState } from "react";
import useStyles from "./OutlinedRadioButton.styles";

type DescriptionLabelPlacement = "leading" | "trailing";

type DescriptionLabelOptions = {
  placement?: DescriptionLabelPlacement;
};

export type OutlinedRadioButtonProps<TValue> = Omit<
  MuiRadioProps,
  "onClick"
> & {
  /**
   * The value associated with the option. This will be provided as the argument to the onChange function
   * provided to  OutlnedRadioGroup
   */
  value: TValue;
  /**
   * Appears in the center of the outlinedn button
   */
  mainLabel: string;
  /**
   * If provided, appears as secondary text on the button
   */
  descriptionLabel?: string;
  /**
   * Configures display of `descriptionLabel`
   */
  descriptionLabelOptions?: DescriptionLabelOptions;
  /**
   * Callback that fires on click of the radio.
   * @param value same as the value prop provided to the button
   * @returns void
   */
  onClick: (value: TValue) => void;
};

const RadioLabel = ({
  mainLabel,
  checked,
  descriptionLabel,
  descriptionLabelPlacement = "trailing",
}: {
  checked?: boolean;
  descriptionLabel?: string;
  descriptionLabelPlacement?: DescriptionLabelPlacement;
  mainLabel: string;
}): JSX.Element => {
  const { classes } = useStyles();

  if (!descriptionLabel || descriptionLabel?.trim().length === 0) {
    return (
      <Typography
        className={classes.mainLabel}
        variant={checked ? "bodyBold" : "body"}
      >
        {mainLabel}
      </Typography>
    );
  } else {
    return (
      <>
        {descriptionLabelPlacement === "leading" && (
          <Typography className={classes.descriptionLabel} variant="bodySmall">
            {descriptionLabel}
          </Typography>
        )}
        <Typography
          className={classes.mainLabel}
          variant={checked ? "bodyBold" : "body"}
        >
          {mainLabel}
        </Typography>
        {descriptionLabelPlacement === "trailing" && (
          <Typography className={classes.descriptionLabel} variant="bodySmall">
            {descriptionLabel}
          </Typography>
        )}
      </>
    );
  }
};

export function OutlinedRadioButton<TValue>(
  props: OutlinedRadioButtonProps<TValue>,
) {
  const {
    value,
    mainLabel,
    descriptionLabel,
    descriptionLabelOptions,
    checked,
    onClick,
    ...restMuiRadioProps
  } = props;

  const { classes, cx } = useStyles();
  const [isFocused, setIsFocused] = useState(false);

  useEffect(() => {
    let field = document.getElementById(mainLabel);
    let label = document.getElementById(`${mainLabel}-label`);
    if (label) {
      // prevent focus passing
      label.addEventListener("focus", (event) => {
        event.preventDefault();
      });

      // activate using spacebar
      label.addEventListener("keyup", (event) => {
        if (event.key === "Enter") {
          field?.click();
        }
      });
    }
  }, [mainLabel]);

  const handleClick = useCallback(() => onClick(value), [onClick, value]);

  return (
    <FormControlLabel
      id={`${value}-label`}
      data-testid={`${value}-${descriptionLabel}-radio-button`}
      onFocus={() => setIsFocused(true)}
      onBlur={() => setIsFocused(false)}
      onClick={() => setIsFocused(false)}
      classes={{
        root: cx(classes.formControl, {
          [classes.focused]: isFocused,
          [classes.selected]: checked,
        }),
        label: classes.formControlLabel,
      }}
      disabled={restMuiRadioProps?.disabled}
      value={value}
      tabIndex={restMuiRadioProps?.tabIndex}
      control={
        <Radio
          id={mainLabel}
          className={classes.button}
          disabled={restMuiRadioProps?.disabled}
          disableRipple
          color="default"
          onClick={handleClick}
          {...restMuiRadioProps}
        />
      }
      label={
        <RadioLabel
          mainLabel={mainLabel}
          checked={checked}
          descriptionLabel={descriptionLabel}
          descriptionLabelPlacement={descriptionLabelOptions?.placement}
        />
      }
    />
  );
}
