import Card, { CardProps } from "@mui/material/Card";
import React from "react";
import { useStyles } from "./SimpleCard.style";

type SimpleCardProps = Omit<CardProps, "raised"> & {
  padding?: "small" | "medium" | "large";
  colorVariant?:
    | "neutral-0"
    | "primary-50"
    | "primary-75"
    | "primary-100"
    | "secondary-50"
    | "secondary-75"
    | "secondary-100"
    | "tertiary-100"
    | "tertiary-200"
    | "tertiary-300"
    | "quaternary-75"
    | "quaternary-100"
    | "quaternary-200";
  elevated?: boolean;
  hasBorder?: boolean;
  children: React.ReactNode;
};

const SimpleCard = ({
  padding = "medium",
  colorVariant = "neutral-0",
  elevated = false,
  hasBorder = false,
  children,
  ...CardProps
}: SimpleCardProps) => {
  const { classes } = useStyles({ padding, colorVariant, elevated, hasBorder });

  return (
    <Card className={classes.card} {...CardProps}>
      {children}
    </Card>
  );
};

export default SimpleCard;
