import CircularProgress from "@mui/material/CircularProgress";
import MUITextField, {
  TextFieldProps as MUITextFieldProps,
} from "@mui/material/TextField";
import { CheckCircleIcon, CloseCircleIcon } from "assets/icons";
import useStyles from "./TextField.styles";

export type TextFieldProps = Omit<MUITextFieldProps, "variant"> &
  Required<Pick<MUITextFieldProps, "label">> &
  Partial<Pick<MUITextFieldProps, "InputProps">> & {
    id: string;
    valid?: boolean;
    hasOptionalText?: boolean;
    loading?: boolean;
  };

const Textfield = ({
  error,
  inputProps: { endAdornment, ...inputProps } = {},
  InputProps,
  InputLabelProps,
  label,
  hiddenLabel,
  multiline,
  hasOptionalText,
  placeholder,
  valid,
  FormHelperTextProps,
  type,
  loading,
  helperText,
  ...rest
}: TextFieldProps) => {
  const { classes, cx } = useStyles();

  const getEndAdornment = () => {
    if (loading) {
      return (
        <>
          <CircularProgress
            size="small"
            data-testid="loading-icon"
            className={cx(classes.inputIcon, {
              [classes.iconMultiline]: multiline,
            })}
          />
          {endAdornment}
        </>
      );
    } else if (error) {
      return (
        <>
          <CloseCircleIcon
            data-testid="error-icon"
            className={cx(classes.inputIcon, classes.iconError, {
              [classes.iconMultiline]: multiline,
            })}
          />
          {endAdornment}
        </>
      );
    } else if (valid) {
      return (
        <>
          <CheckCircleIcon
            data-testid="valid-icon"
            className={cx(classes.inputIcon, {
              [classes.iconMultiline]: multiline,
            })}
          />
          {endAdornment}
        </>
      );
    } else {
      return endAdornment;
    }
  };

  const labelWithOptionalTag = (
    <span>
      {label}
      <span className={classes.optionalText}>(Optional)</span>
    </span>
  );

  return (
    <MUITextField
      error={error && !loading}
      label={hasOptionalText ? labelWithOptionalTag : label}
      placeholder={placeholder}
      type={type ? type : undefined}
      InputLabelProps={{
        error: false,
        classes: {
          asterisk: classes.required,
        },
        ...InputLabelProps,
        className: cx(
          classes.label,
          InputLabelProps && InputLabelProps.className,
          { "sr-only": hiddenLabel },
        ),
        shrink: true, // this needs to be true, or placeholder text does not show
      }}
      InputProps={{
        endAdornment: getEndAdornment(),
        ...InputProps,
        className: cx(classes.input, InputProps?.className, {
          valid: valid && !error && !loading,
        }),
      }}
      inputProps={{
        ...inputProps,
        className: cx(classes.inputField, inputProps?.className),
      }}
      FormHelperTextProps={{
        ...FormHelperTextProps,
        className: classes.helperText,
      }}
      multiline={multiline}
      {...(helperText && !(loading && error) && { helperText })}
      {...rest}
    />
  );
};

export default Textfield;
