import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import { ChevronDownSmallIcon } from "assets/icons";
import { Typography } from "comp-lib/Typography";
import React, { useEffect, useState } from "react";
import useStyles from "./DrawerAccordion.styles";

export type DrawerAccordionProps = {
  content: NonNullable<React.ReactNode>;
  openTitle: string;
  closedTitle?: string;
  id?: string;
  showAll?: boolean;
  unmountOnExit?: boolean;
  className?: string;
  onChange?: (expanded: boolean) => void;
  classes?: Partial<
    Record<
      | "root"
      | "labelContainer"
      | "labelContent"
      | "label"
      | "content"
      | "arrow",
      string
    >
  >;
};

const DrawerAccordion = ({
  content,
  openTitle,
  closedTitle = openTitle,
  showAll = false,
  id = "panel",
  unmountOnExit = false,
  className,
  onChange,
  ...rest
}: DrawerAccordionProps) => {
  const [expanded, setExpanded] = useState(false);
  const { classes, cx } = useStyles();
  useEffect(() => {
    setExpanded(showAll);
  }, [showAll]);

  const handleOnChange = (event: React.ChangeEvent<{}>, expanded: boolean) => {
    setExpanded(expanded);
    onChange?.(expanded);
  };

  const labelTitle = expanded ? openTitle : closedTitle;

  return (
    <Accordion
      onChange={handleOnChange}
      expanded={expanded}
      className={cx(className, classes.drawer)}
      classes={{
        root: cx(classes.root, rest.classes?.root),
      }}
      TransitionProps={{ unmountOnExit }}
      key={`${id}`}
    >
      <AccordionSummary
        classes={{
          root: cx(classes.labelRoot, rest.classes?.labelContainer),
          content: cx(classes.labelContent, rest.classes?.labelContent),
          expandIconWrapper: classes.iconRoot,
        }}
        expandIcon={
          <ChevronDownSmallIcon
            className={cx(classes.arrow, rest.classes?.arrow)}
          />
        }
        aria-controls={`${id}-details`}
        id={`${id}-summary`}
      >
        <Typography className={rest.classes?.label} variant="bodySmall">
          {labelTitle ?? ""}
        </Typography>
      </AccordionSummary>
      <AccordionDetails
        classes={{
          root: cx(classes.contentRoot, rest.classes?.content),
        }}
        id={`${id}-details`}
      >
        {content}
      </AccordionDetails>
    </Accordion>
  );
};

export default DrawerAccordion;
