import { ArrowLeftIcon } from "assets/icons";
import { Button } from "comp-lib/Button";
import useBreakpoint from "hooks/useBreakpoint";
import useStyles from "./Breadcrumbs.styles";
import { Breadcrumb } from "./index";

type BreadcrumbsProps = {
  breadcrumbs: Breadcrumb[];
  classOverrides?: string;
  navigate: (path: string) => any;
};

const Breadcrumbs = ({
  breadcrumbs,
  classOverrides,
  navigate,
}: BreadcrumbsProps) => {
  const { classes, cx } = useStyles();
  const { smDown } = useBreakpoint();
  const displayDoesNotExistForLastBreadcrumb =
    !breadcrumbs[breadcrumbs.length - 1].display;
  return (
    <nav
      className={cx(classes.breadcrumbsContainer, classOverrides)}
      aria-label="breadcrumbs"
    >
      {smDown && breadcrumbs.length === 2 ? (
        <div className={classes.xsBtnWithChevronFocusWrapper}>
          <Button
            className={classes.xsBtnWithChevronFocus}
            focusVisibleClassName={classes.focus}
            component="a"
            role="link"
            onClick={() => navigate(breadcrumbs[0].link)}
            size="xsmall"
            startIcon={<ArrowLeftIcon className={classes.breadcrumbBackIcon} />}
            variant="tertiary"
          >
            {breadcrumbs[0].display}
          </Button>
        </div>
      ) : (
        <ol className={classes.breadCrumbOrderedList}>
          {breadcrumbs.map(({ display, link }, index) => {
            const isLastCrumb = index === breadcrumbs.length - 1;
            const isSecondToLastCrumb = index === breadcrumbs.length - 2;
            const shouldNotRenderBackSlash =
              (smDown && isSecondToLastCrumb) ||
              displayDoesNotExistForLastBreadcrumb;
            if (isLastCrumb) {
              return smDown ? null : (
                <li
                  key={link}
                  className={classes.lastCrumb}
                  aria-current="location"
                >
                  {display}
                </li>
              );
            }
            return (
              <li key={link} className={classes.buttonContainer}>
                <Button
                  focusVisibleClassName={classes.focus}
                  component="a"
                  role="link"
                  onClick={() => navigate(link)}
                  size={smDown ? "xsmall" : "small"}
                  variant="tertiary"
                  className={classes.linkOverrides}
                >
                  {display}
                </Button>
                {/* on mobile, we don't display the last breadcrumb, so we shouldn't show a slash after the second to last breadcrumb */}
                {/* We also don't want to show the slash if the category/type wasn't found and the breadcrumb's display value is '' */}
                {shouldNotRenderBackSlash ? null : (
                  <span className={classes.breadcrumbsSlash} aria-hidden="true">
                    /
                  </span>
                )}
              </li>
            );
          })}
        </ol>
      )}
    </nav>
  );
};

export default Breadcrumbs;
