import {
  Switch as MuiSwitch,
  SwitchProps as MuiSwitchProps,
} from "@mui/material";
import { Typography } from "comp-lib/Typography";
import useStyles from "./Switch.styles";

type SwitchProps = MuiSwitchProps & {
  label: string;
  hint?: string;
};

const Switch = ({ label, hint, ...switchProps }: SwitchProps) => {
  const { classes } = useStyles();

  return (
    <>
      <div>
        <label className={classes.switchAndLabel}>
          <MuiSwitch
            className={classes.base}
            data-testid="switch"
            {...switchProps}
          />
          <Typography className={classes.label} variant="body">
            {label}
          </Typography>
        </label>
      </div>
      {hint && (
        <Typography className={classes.hint} variant="bodySmall">
          Hint/{hint}
        </Typography>
      )}
    </>
  );
};

export default Switch;
