import { Box } from "@mui/material";
import Autocomplete from "@mui/material/Autocomplete";
import type { FilterOptionsState } from "@mui/material/useAutocomplete";
import { CheckmarkIcon, ChevronDownIcon } from "assets/icons";
import { TextField } from "comp-lib/TextField";
import { useState } from "react";
import useStyles from "./AutoCompleteInput.styles";

type AutoCompleteInputProps<OptionType> = {
  className?: string;
  optionsList: OptionType[];
  label: string;
  placeholder?: string;
  getOptionLabel: (option: OptionType) => string;
  getOptionSelected: (option: OptionType, value: OptionType) => boolean;
  onChange: (data: OptionType | null) => void;
  filterOptions?: (
    options: OptionType[],
    state: FilterOptionsState<OptionType>,
  ) => OptionType[];
  value: OptionType | null;
};

// need to extend due to JSX syntax ambiguities: https://github.com/microsoft/TypeScript/issues/15713
const AutoCompleteInput = <OptionType extends unknown>({
  optionsList,
  label,
  getOptionLabel,
  getOptionSelected,
  placeholder,
  onChange,
  filterOptions,
  value,
}: AutoCompleteInputProps<OptionType>) => {
  const { classes } = useStyles();

  const [chevron, setChevron] = useState(<ChevronDownIcon />);

  return (
    <Box>
      <Autocomplete
        classes={{ popper: classes.popper }}
        openOnFocus
        onOpen={() =>
          setChevron(<ChevronDownIcon className={classes.rotateUp} />)
        }
        onClose={() => setChevron(<ChevronDownIcon />)}
        filterOptions={filterOptions}
        onChange={(event, newValue) => {
          onChange(newValue);
        }}
        disableListWrap
        className={classes.textField}
        id="driveway-auto-complete-input"
        options={optionsList}
        getOptionLabel={getOptionLabel}
        value={value}
        isOptionEqualToValue={getOptionSelected}
        renderInput={(params) => (
          <TextField
            {...params}
            label={label}
            placeholder={placeholder}
            valid={value !== null}
            InputProps={{
              ...params.InputProps,
              className: classes.inputStyles,
              endAdornment: (
                <div className={value ? classes.iconsInput : ""}>
                  {value && <CheckmarkIcon />}
                  {chevron}
                </div>
              ),
            }}
          />
        )}
      />
    </Box>
  );
};

export default AutoCompleteInput;
