import { Drawer } from "@mui/material";
import { CloseIcon } from "assets/icons";
import { Button } from "comp-lib/Button";
import { IconButton } from "comp-lib/IconButton";
import { Typography } from "comp-lib/Typography";
import useStyles from "./ActionSheet.styles";

type ActionSheetProps = {
  title: string;
  open: boolean;
  toggleDrawer: (arg0: React.KeyboardEvent | React.MouseEvent) => void;
  content: JSX.Element;
  ctaButtonText?: string;
  closeAriaLabel?: string;
};

const ActionSheet = (props: ActionSheetProps) => {
  const { open, toggleDrawer, content, title, ctaButtonText, closeAriaLabel } =
    props;
  const { classes } = useStyles();

  return (
    <Drawer
      open={open}
      anchor="bottom"
      onClose={toggleDrawer}
      className={classes.actionSheet}
      transitionDuration={{ enter: 500, exit: 750 }}
    >
      <div className={classes.actionSheetTitle}>
        <Typography variant="overlineLarge">{title}</Typography>
        <IconButton
          className={classes.closeButton}
          size="xsmall"
          aria-label={closeAriaLabel ? closeAriaLabel : "clear search bar"}
          variant="secondary-alt"
          borderless
          onClick={toggleDrawer}
        >
          <CloseIcon />
        </IconButton>
      </div>
      <div className={classes.actionSheetContent}>
        {content}
        {ctaButtonText && (
          <Button
            className={classes.ctaButton}
            onClick={toggleDrawer}
            variant="primary"
          >
            {ctaButtonText}
          </Button>
        )}
      </div>
    </Drawer>
  );
};

export default ActionSheet;
