import type { ButtonProps } from "comp-lib/Button";
import { IconButton } from "comp-lib/IconButton";
import {
  Animation,
  createAnimation,
  SpriteAnimation,
} from "comp-lib/SpriteAnimation";
import { Typography } from "comp-lib/Typography";
import { useMemo, useState } from "react";
import useStyles from "./FavoriteIcon.styles";

type FavoriteIconProps = Omit<
  ButtonProps,
  "variant" | "borderless" | "size" | "bare"
> & {
  saved: boolean;
  width?: number;
  height?: number;
  count?: number;
  sprite: string;
  isLoading?: boolean;
  onClick: () => void;
};

//This is at the module scope so the animation values will only be calculated
// once at runtime.
let generateFavoriteAnimation: ({
  sprite,
  width,
  height,
}: {
  sprite: string;
  width?: number;
  height?: number;
}) => Animation;
try {
  generateFavoriteAnimation = ({ sprite, width = 62, height = 62 }) =>
    createAnimation({
      spritePath: sprite,
      frameWidth: width,
      frameHeight: height,
      totalFrames: 39,
      totalSeconds: 0.6,
      debug: false,
      segments: {
        favorite: {
          startFrame: 0,
          endFrame: 25,
        },
        unfavorite: {
          startFrame: 25,
          endFrame: 38,
        },
        loading: {
          startFrame: 24,
          endFrame: 38,
          delay: 800, //wait for a few ms before showing the loading animation
          duration: 600, //same as the unfavorite animation but slower
          direction: "alternate-reverse", //play reverse then forward
          iterations: Infinity, //play until canceled
        },
      },
    });
} catch (error) {
  console.error(error);
}

const FavoriteCount = ({ count }: { count: number }) => {
  const { classes } = useStyles();
  if (count < 1) {
    return null;
  }
  return (
    <div className={classes.favoriteCount}>
      <Typography variant="microcopyBold" data-testid="favorite-interest-count">
        {count} {count === 1 ? "person saved" : "people saved"}
      </Typography>
    </div>
  );
};

const FavoriteIcon = ({
  saved = false,
  isLoading = false,
  count = 0,
  width,
  height,
  sprite,
  onClick,
  className,
  ...props
}: FavoriteIconProps) => {
  const { classes, cx } = useStyles();

  const [isUserAction, setIsUserAction] = useState(false);

  const favoriteAnimation = useMemo(
    () => generateFavoriteAnimation({ sprite, width, height }),
    [height, sprite, width],
  );

  const handleClick = () => {
    setIsUserAction(true);
    onClick();
  };

  return (
    <div className={cx(classes.wrapper, className)}>
      {count > 0 && <FavoriteCount count={count} />}
      <IconButton
        aria-label="Favorite Icon"
        aria-pressed={saved}
        className={cx(classes.circle)}
        data-testid="favorite-icon"
        onClick={handleClick}
        size="small"
        variant="secondary-alt"
        {...props}
      >
        <SpriteAnimation
          data-testid={saved ? "heart-sprite-filled" : "heart-sprite-unfilled"}
          animation={favoriteAnimation}
          segmentToPlay={
            isLoading ? "loading" : saved ? "favorite" : "unfavorite"
          }
          animate={isLoading || isUserAction}
        />
      </IconButton>
    </div>
  );
};

export default FavoriteIcon;
export type { FavoriteIconProps };
