import { LinearProgress } from "@mui/material";
import { useEffect, useState } from "react";
import useStyles from "./LoadingBarAnimation.styles";

type LoadingBarAnimationProps = {
  //please enter in seconds. ex. 1 minute = 60 seconds
  totalTime: number;
  //please enter a number from 1-100.
  maxPercentage: number;
};

const LoadingBarAnimation = ({
  totalTime,
  maxPercentage,
}: LoadingBarAnimationProps) => {
  const { classes } = useStyles();
  const [progress, setProgress] = useState(0);

  useEffect(() => {
    const interval = 2000;
    const intervalCount = (totalTime * 1000) / interval;
    const progressIncrement = maxPercentage / intervalCount;

    const timer = setInterval(() => {
      setProgress((progress) => {
        return Math.min(progress + progressIncrement, maxPercentage);
      });
      if (progress >= maxPercentage) {
        clearInterval(timer);
      }
    }, interval);

    return () => clearInterval(timer);
  }, [progress, maxPercentage, totalTime]);

  return (
    <LinearProgress
      classes={{
        root: classes.root,
        colorPrimary: classes.unfilledBarOverride,
      }}
      variant="determinate"
      value={progress}
    />
  );
};

export default LoadingBarAnimation;
