import { Button, ButtonProps } from "comp-lib/Button";
import { forwardRef, Ref } from "react";
import useStyles from "./IconButton.styles";

export const ICON_BUTTON_VERSION = "1.1.0";

export type IconButtonProps = ButtonProps & {
  disableTouchTarget?: boolean;
};

// temp solution for when icon buttons have dedicated classes for sizes
const ButtonLength = {
  xsmall: 32,
  small: 40,
  medium: 48,
  large: 64,
};

const SvgSize = {
  xsmall: 16,
  small: 24,
  medium: 24,
  large: 24,
};

const BorderRadius = {
  xsmall: 4,
  small: 4,
  medium: 8,
  large: 8,
};

const IconButton = forwardRef<HTMLButtonElement, IconButtonProps>(
  (
    {
      children,
      className,
      disableTouchTarget = false,
      size = "medium",
      ...rest
    }: IconButtonProps,
    ref: Ref<HTMLButtonElement>,
  ) => {
    const { classes, cx } = useStyles({
      buttonLength: ButtonLength[size],
      borderRadius: BorderRadius[size],
      svgSize: SvgSize[size],
      disableTouchTarget,
    });

    return (
      <Button className={cx(classes.iconButton, className)} ref={ref} {...rest}>
        <div className={classes.iconContainer}>{children}</div>
      </Button>
    );
  },
);

export default IconButton;
