import { Checkbox, CheckboxProps, FormControlLabel } from "@mui/material";
import { CheckmarkIcon } from "assets/icons";
import useStyles from "./SimpleCheckbox.styles";

export type SimpleCheckboxProps = {
  checkboxProps?: CheckboxProps;
  label?: string;
  error?: boolean;
  required?: boolean;
  hasOptionalText?: boolean;
  helperText?: string;
};
const SimpleCheckbox = ({
  checkboxProps,
  label,
  error,
  required,
  hasOptionalText,
  helperText,
}: SimpleCheckboxProps) => {
  const { classes, cx } = useStyles();
  const rootWithLabelStyle = helperText
    ? cx(classes.helperRoot, classes.rootWithLabel)
    : classes.rootWithLabel;
  const checkedIcon = (
    <CheckmarkIcon className={classes.checkboxOutlinedIcon} />
  );

  const labelVariations = (
    <span className={helperText ? classes.helperTextContainer : undefined}>
      <span>
        {label}
        {hasOptionalText && !required ? (
          <span className={classes.optionalText}>(Optional)</span>
        ) : required && !hasOptionalText ? (
          <span className={classes.errorColor}>*</span>
        ) : null}
      </span>
      {helperText ? (
        <span
          className={
            error
              ? cx(classes.helperText, classes.errorColor)
              : classes.helperText
          }
        >
          {helperText}
        </span>
      ) : null}
    </span>
  );

  const setClass = (): string => {
    let newClass: string = "";
    if (error) {
      newClass = cx(classes.uncheckedBox, classes.errorColor);
    } else {
      newClass = classes.uncheckedBox;
    }
    if (checkboxProps?.disabled) {
      newClass = cx(newClass, classes.uncheckedBoxDisabled);
    }
    return newClass;
  };

  return label ? (
    <FormControlLabel
      label={labelVariations}
      labelPlacement="end"
      classes={{
        root: error ? classes.errorFormControl : classes.formControl,
        label: error ? classes.errorFormControlLabel : classes.formControlLabel,
      }}
      control={
        <Checkbox
          className={rootWithLabelStyle}
          disableRipple
          checkedIcon={checkedIcon}
          icon={<span className={setClass()}></span>}
          inputProps={{
            "aria-label": label,
          }}
          {...checkboxProps}
        />
      }
    />
  ) : (
    <Checkbox
      className={classes.root}
      disableRipple
      checkedIcon={checkedIcon}
      icon={<span className={setClass()}></span>}
      inputProps={{ "aria-label": label }}
      {...checkboxProps}
    />
  );
};

export default SimpleCheckbox;
