import { ClickAwayListener } from "@mui/material";
import MUITooltip, { TooltipProps } from "@mui/material/Tooltip";
import { CloseIcon } from "assets/icons";
import { Button } from "comp-lib/Button";
import React, { useEffect, useRef, useState } from "react";
import useStyles from "./Tooltip.styles";

/**
   *
   ## Decision Context
  After a discussion with the team about accessibility, we decided the
  right decision is for us to not open these on "hover" and do it by
  clicking or tapping. The way this tooltip functions is closer to a
  modal opened by a button vs. a traditional tooltip. The difference
  between the two being a traditional tooltip is short text describing
  what the hovered item is vs. a large amount of text with actions,
  buttons, lists, etc. which is closer to a modal.

  One big reason for this is that when tabbing through items on a
  keyboard, it's standard practice that you activate the same "hover"
  effects that you would when you hover over something with your mouse.
  The problem is that these massive tooltips would open up as users
  would tab through content and they would be forced to tab through all
  the contents of the tooltips to close it, creating a burdened UX for
  them.

  Another thing is that on mobile, the default behavior of Tooltips
  defined by MUI is for them to show up when the user holds their finger
  on the tooltip trigger, which would cause the tooltip to show up until
  1.5s after they let go of the trigger. This obviously wouldn't work
  for the use-case we're designing for, so mobile also follows a
  click-close format.

  In the future if we ever have simpler, smaller, no-action tooltips
  being used in a new way, we should revisit the hover vs. click topic.
  **/
const Tooltip = (props: TooltipProps) => {
  const { classes } = useStyles();
  const popperRef = useRef<any>(null);
  const [isOpen, setIsOpen] = useState(false);

  const setOpened = (e?: any) => {
    setIsOpen(true);

    if (props.onOpen) {
      props.onOpen(e);
    }
  };

  const setClosed = () => {
    setIsOpen(false);
  };

  const KeyboardMouseClicked = (e: React.KeyboardEvent<Element>) => {
    return e.key === " " || e.key === "Enter";
  };

  const handleKeyboardOnFocus = (e: React.KeyboardEvent<Element>) => {
    if (KeyboardMouseClicked(e)) {
      setOpened();
    }
  };

  useEffect(() => {
    const KeyListener = (e: any) => {
      if (e.key === "Escape") {
        setClosed();
      }

      // If someone does the keyboard-equivalent of "clicking" outside of the tooltip,
      // we want that to close it. Otherwise it's possible to get multiple tooltips open.
      // NOTE: This was an issue created by setting "disablePortal" to "True" on the popperRef.
      // disablePortal needed to be changed because tab order was messed up with it set
      // to "False" (it's default).
      if (KeyboardMouseClicked(e)) {
        const insideOfTooltip = popperRef?.current?.popper.contains(e.target);

        if (!insideOfTooltip) {
          setClosed();
        }
      }
    };

    if (isOpen) {
      document.addEventListener("keydown", KeyListener);
    }

    return () => {
      if (isOpen) {
        document.removeEventListener("keydown", KeyListener);
      }
    };
  }, [isOpen]);

  const tooltipContent = (
    <div className={classes.closeButtonContainer}>
      <Button
        className={classes.closeButton}
        id="toolTipClose"
        borderless
        onClick={setClosed}
        variant="secondary-alt"
        aria-label={"Close"}
      >
        <CloseIcon />
      </Button>
      <div tabIndex={-1} className={classes.tooltipText}>
        {props.title}
      </div>
    </div>
  );

  const clickAwayHandler = () => {
    return isOpen ? setClosed() : null;
  };

  return (
    <ClickAwayListener onClickAway={clickAwayHandler}>
      <div
        className={classes.tooltipOpener}
        onClick={(e) => e.preventDefault()}
      >
        <MUITooltip
          classes={{
            tooltip: classes.tooltip,
            arrow: classes.arrow,
            tooltipPlacementBottom: classes.tooltipPlacementBottom,
            tooltipPlacementTop: classes.tooltipPlacementTop,
            tooltipPlacementRight: classes.tooltipPlacementRight,
            tooltipPlacementLeft: classes.tooltipPlacementLeft,
          }}
          PopperProps={{
            disablePortal: true,
            popperRef: popperRef,
          }}
          open={isOpen}
          arrow={true}
          disableFocusListener
          disableHoverListener
          disableTouchListener
          onClick={setOpened}
          onKeyDown={handleKeyboardOnFocus}
          {...props}
          title={tooltipContent}
        />
      </div>
    </ClickAwayListener>
  );
};

export default Tooltip;
