import { CircularProgress } from "@mui/material";
import { makeStyles } from "tss-react/mui";

const useStyles = makeStyles({ name: "MuiDWLoading" })((theme) => ({
  loadingIcon: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    minHeight: "600px",
    [theme.breakpoints.up("lg")]: {
      minHeight: "800px",
    },
  },
  fullHeight: {
    minHeight: "100vh",
  },
}));

type LoadingProps = {
  className?: string;
  fullHeight?: boolean;
};

const Loading = ({ className, fullHeight }: LoadingProps) => {
  const { classes, cx } = useStyles();

  return (
    <div
      className={cx([
        className,
        classes.loadingIcon,
        fullHeight && classes.fullHeight,
      ])}
      data-testid="loading-spinner"
    >
      <CircularProgress />
    </div>
  );
};

export default Loading;
