import type { RadioProps } from "@mui/material";
import type { RadioButtonVariant } from "comp-lib/RadioButton";
import { ReactNode } from "react";
import CompactGroup from "./CompactGroup";
import OutlinedGroup from "./OutlinedGroup";

export type RadioGroupOptions = {
  radioButtonProps?: RadioProps;
  value: string;
  textContent: {
    label?: string;
    selectedValue?: string;
    mainLabel?: string;
    descriptionLabel?: string;
  };
  subContent?: ReactNode;
  withSuccessIcon?: boolean;
  helperText?: string;
};

export type RadioButtonGroupProps = {
  totalButtons?: number;
  boldLabelHeading?: boolean;
  variant: RadioButtonVariant;
  options?: RadioGroupOptions[];
  onChange: (value: string) => void;
  groupTextContent: {
    description?: string;
    className?: string;
    groupLabel?: string;
    errorText?: string;
    selectedValue?: string;
  };
  groupBooleanChecks?: {
    disabled: boolean;
  };
  scrollIntoViewOptions?: ScrollIntoViewOptions | boolean;
  classes?: Record<string, string>;
  id?: string;
  tooltip?: JSX.Element;
};

type Element = {
  [property in RadioButtonVariant]: JSX.Element;
};

/**
 * @deprecated Use `BasicRadioGroup or OutlinedRadioGroup` instead.
 */
const RadioButtonGroup = (props: RadioButtonGroupProps): JSX.Element => {
  const {
    totalButtons,
    boldLabelHeading,
    variant,
    options,
    onChange,
    groupTextContent,
    groupBooleanChecks,
    scrollIntoViewOptions,
    classes,
    id,
    tooltip,
  } = props;

  const element: Element = {
    compact: (
      <CompactGroup
        id={id}
        tooltip={tooltip}
        variant="compact"
        options={options}
        onChange={onChange}
        groupTextContent={groupTextContent}
        groupBooleanChecks={groupBooleanChecks}
        scrollIntoViewOptions={scrollIntoViewOptions}
        classes={classes}
      />
    ),
    outlined: (
      <OutlinedGroup
        boldLabelHeading={boldLabelHeading}
        totalButtons={totalButtons}
        variant="outlined"
        onChange={onChange}
        options={options}
        groupTextContent={groupTextContent}
      />
    ),
    outlinedLarge: (
      <OutlinedGroup
        totalButtons={totalButtons}
        variant="outlinedLarge"
        onChange={onChange}
        options={options}
        groupTextContent={groupTextContent}
      />
    ),
  };
  return element[variant];
};

export default RadioButtonGroup;
