import type { RadioProps } from "@mui/material";
import type { RadioGroupOptions } from "comp-lib/RadioButtonGroup";
import CompactRadioButton from "./CompactRadioButton";
import OutlinedRadioButton from "./OutlinedRadioButton";

export type RadioButtonProps = {
  radioButtonProps?: RadioProps;
  variant: RadioButtonVariant;
  value: string;
  error?: boolean;
  hasOptionalText?: boolean;
  required?: boolean;
  helperText?: string;

  reverseLabel?: boolean;
  withSuccessIcon?: boolean;
} & Pick<RadioGroupOptions, "textContent">;

type Element = {
  [property in RadioButtonVariant]: JSX.Element;
};

export type RadioButtonVariant = "compact" | "outlined" | "outlinedLarge";

/**
 * @deprecated Use `BasicRadioGroup or OutlinedRadioGroup` instead.
 */
const RadioButton = (props: RadioButtonProps) => {
  const {
    value,
    variant,
    radioButtonProps,
    textContent,
    reverseLabel,
    withSuccessIcon,
    error,
    required,
    hasOptionalText,
    helperText,
  } = props;

  const element: Element = {
    compact: (
      <CompactRadioButton
        value={value}
        label={textContent.label!}
        error={error}
        hasOptionalText={hasOptionalText}
        required={required}
        radioButtonProps={radioButtonProps}
        helperText={helperText}
      />
    ),
    outlined: (
      <OutlinedRadioButton
        radioButtonProps={radioButtonProps}
        mainLabel={textContent.mainLabel!}
        descriptionLabel={textContent.descriptionLabel!}
        reverseLabel={reverseLabel}
        withSuccessIcon={withSuccessIcon}
        currentSelection={textContent.selectedValue!}
        value={value}
        size="small"
      />
    ),
    outlinedLarge: (
      <OutlinedRadioButton
        radioButtonProps={radioButtonProps}
        mainLabel={textContent.mainLabel!}
        descriptionLabel={textContent.descriptionLabel!}
        reverseLabel={reverseLabel}
        withSuccessIcon={withSuccessIcon}
        currentSelection={textContent.selectedValue!}
        value={value}
        size="large"
      />
    ),
  };
  return element[variant];
};

export default RadioButton;
