import Fade from "@mui/material/Fade";
import Slide from "@mui/material/Slide";
import Snackbar from "@mui/material/Snackbar";
import SnackbarContent from "@mui/material/SnackbarContent";
import { TransitionProps } from "@mui/material/transitions";
import {
  AlertCircleIcon,
  AlertTriangleIcon,
  CheckCircleIcon,
  CloseIcon,
  InfoIcon,
} from "assets/icons";
import { Button } from "comp-lib/Button";
import { IconButton } from "comp-lib/IconButton";
import { AlertTypes } from "comp-lib/models/alert-types";
import { Typography } from "comp-lib/Typography";
import { SyntheticEvent, useCallback } from "react";
import type { ToastProps } from ".";
import useStyles from "./Toast.styles";

const AlertIcons = {
  [AlertTypes.Warning]: AlertTriangleIcon,
  [AlertTypes.Success]: CheckCircleIcon,
  [AlertTypes.Informative]: InfoIcon,
  [AlertTypes.Error]: AlertCircleIcon,
};

const Toast = ({
  isOpen,
  setIsOpen,
  title,
  message,
  actionBtnProps,
  alertType = AlertTypes.Informative,
  onScreenDuration = 6000,
  closeOnClickAway = false,
  travelDirection = "up",
  anchorOrigin = { horizontal: "right", vertical: "bottom" },
  className,
  id,
}: ToastProps): JSX.Element => {
  const { classes, cx } = useStyles({ alertType, anchorOrigin });

  const handleClose = useCallback(
    (_evt?: Event | SyntheticEvent<any, Event>, reason?: string) => {
      if (!closeOnClickAway && reason === "clickaway") {
        return;
      }
      setIsOpen(false);
    },
    [closeOnClickAway, setIsOpen],
  );

  const Icon = AlertIcons[alertType] || InfoIcon;

  const Transition = useCallback(
    (
      props: TransitionProps & {
        children: React.ReactElement<any, any>;
      },
    ) => {
      return (
        <Slide
          {...props}
          direction={travelDirection}
          timeout={{ enter: 500, exit: 500 }}
        >
          <div>
            <Fade
              {...props}
              className={classes.fadeIn}
              timeout={{ enter: 500, exit: 500 }}
            />
          </div>
        </Slide>
      );
    },
    [travelDirection, classes.fadeIn],
  );

  return (
    <Snackbar
      id={id}
      className={
        !Transition
          ? cx(classes.snackbar, classes.snackBarBorder, className)
          : cx(classes.snackbar, className)
      }
      TransitionComponent={Transition}
      open={isOpen}
      autoHideDuration={onScreenDuration}
      onClose={handleClose}
      anchorOrigin={anchorOrigin}
      aria-live="assertive"
      data-testid="toast"
      role="alert"
    >
      <div className={classes.snackbarWrapper}>
        <SnackbarContent
          className={classes.snackbarContent}
          message={
            <>
              <Typography variant="bodyBold">{title}</Typography>
              <Typography variant="body">{message}</Typography>
              {actionBtnProps && (
                <Button variant="tertiary" size="small" {...actionBtnProps}>
                  {actionBtnProps.text}
                </Button>
              )}
            </>
          }
          action={
            <>
              <Icon
                data-testid={`${alertType.toLowerCase()}-icon`}
                className={classes.icon}
              />
              <IconButton
                size="small"
                aria-label="close"
                borderless
                color="inherit"
                onClick={handleClose}
                variant="secondary-alt"
                className={classes.iconButton}
              >
                <CloseIcon />
              </IconButton>
            </>
          }
        />
      </div>
    </Snackbar>
  );
};

export default Toast;
