import { ToggleButton, ToggleButtonProps } from "@mui/material";
import useStyles from "./ToggleBtn.styles";

const ToggleBtn = ({
  children,
  value,
  classes,
  ...rest
}: ToggleButtonProps) => {
  const { classes: toggleBtnClasses, cx } = useStyles();

  return (
    <ToggleButton
      classes={{
        root: cx([toggleBtnClasses.segment]),
        ...classes,
      }}
      value={value}
      {...rest}
    >
      {children}
    </ToggleButton>
  );
};

export default ToggleBtn;
export type { ToggleButtonProps };
