import {
  AlertCircleIcon,
  AlertTriangleIcon,
  CheckCircleIcon,
  CloseIcon,
  InfoIcon,
} from "assets/icons";
import { IconButton } from "comp-lib/IconButton";
import { Typography } from "comp-lib/Typography";
import { HTMLAttributes, ReactNode, SyntheticEvent } from "react";
import useStyles from "./Alert.styles";

export type AlertProps = HTMLAttributes<HTMLDivElement> & {
  id?: string;
  variant: "error" | "success" | "warning" | "info";
  message: ReactNode;
  closeAlert?: (e: SyntheticEvent) => void;
  onClick?: () => void;
  isAnchored?: boolean;
  enableCloseIcon?: boolean;
};

const Alert = ({
  id,
  variant,
  message,
  closeAlert,
  onClick,
  isAnchored = false,
  enableCloseIcon = false,
  ...props
}: AlertProps) => {
  const { classes, cx } = useStyles();
  if (!message) {
    return null;
  }

  let variantClass, AlertIcon;

  switch (variant) {
    case "error":
      variantClass = classes.error;
      AlertIcon = () => <AlertCircleIcon />;
      break;
    case "success":
      variantClass = classes.success;
      AlertIcon = () => <CheckCircleIcon />;
      break;
    case "warning":
      variantClass = classes.warning;
      AlertIcon = () => <AlertTriangleIcon />;
      break;
    case "info":
    default:
      variantClass = classes.info;
      AlertIcon = () => <InfoIcon />;
  }

  const isInlineWarningOrError =
    !isAnchored && ["error", "warning"].includes(variant);

  return (
    <div
      id={id}
      onClick={onClick}
      className={cx({
        [classes.alert]: true,
        [variantClass]: true,
        [classes.bottomMargin]: isInlineWarningOrError,
        [classes.anchored]: isAnchored,
        [classes.alertClose]: enableCloseIcon,
        [classes.hover]: !!onClick,
      })}
      {...props}
    >
      <div className={classes.msgIcon}>
        <AlertIcon />
      </div>
      <Typography className={classes.alertMsg} variant="body">
        {message}
      </Typography>
      {enableCloseIcon && (
        <IconButton
          className={classes.closeIconButton}
          aria-label="close"
          borderless
          onClick={closeAlert}
          variant="secondary"
          size="small"
        >
          <CloseIcon className={classes.closeIcon} />
        </IconButton>
      )}
    </div>
  );
};

export default Alert;
