import { useMediaQuery } from "@mui/material";
import { Button, ButtonProps } from "comp-lib/Button";
import { Modal, ModalProps } from "comp-lib/Modal";
import { Typography } from "comp-lib/Typography";
import { useAdvancedModalStyles } from "./AdvancedModal.styles";

export const defaultImageHeights = {
  large: 200,
  medium: 140,
  small: 120,
  mobile: 100,
  exSmall: 80,
};

export type ModalImg = {
  alt: string;
  hideOnMobile?: boolean;
  mobileHeight?: string | number; // number should be in px
  src: string;
  tabletUpHeight?: string | number; // number should be in px
};

export type AdvancedModalProps = React.HTMLAttributes<HTMLDivElement> & {
  children: string | JSX.Element;
  headline?: string;
  image?: ModalImg;
  ModalProps: ModalProps;
  primaryButton?: ButtonProps; // full-width CTA above "Close" button
  secondaryButton?: ButtonProps; // full-width CTA above "Close" button (secondary variant by default)
};

const AdvancedModal = ({
  children,
  headline,
  image,
  ModalProps,
  primaryButton,
  secondaryButton,
  ...props
}: AdvancedModalProps) => {
  const { size = "medium" } = ModalProps;
  const { classes, cx, theme } = useAdvancedModalStyles();
  const mdUp = useMediaQuery(theme.breakpoints.up("md"));
  const xsOnly = useMediaQuery(theme.breakpoints.only("xs"));
  const imageHeightOnTabletOrLarger =
    image?.tabletUpHeight || defaultImageHeights[size];
  const defaultMobileImgHeight = xsOnly
    ? defaultImageHeights.exSmall
    : defaultImageHeights.mobile;
  const imageHeightOnMobile = image?.mobileHeight || defaultMobileImgHeight;

  return (
    <Modal
      {...ModalProps}
      closeActionStyle={
        (primaryButton && classes.closeButton) || ModalProps.closeActionStyle
      }
      size={size}
    >
      <section className={classes.container} {...props}>
        {image && (
          <div>
            <img
              alt={image.alt}
              height={mdUp ? imageHeightOnTabletOrLarger : imageHeightOnMobile}
              className={cx([
                image.hideOnMobile && classes.imageHiddenMobile,
                classes.image,
              ])}
              src={image.src}
            />
          </div>
        )}
        {headline && (
          <Typography variant="h4" className={classes.headline}>
            {headline}
          </Typography>
        )}
        {typeof children === "string" ? (
          <Typography className={classes.modalText} variant="body">
            {children}
          </Typography>
        ) : (
          children
        )}
        <section className={classes.buttonRow}>
          {secondaryButton && (
            <Button
              id="modal-secondary-btn"
              {...secondaryButton}
              className={classes.secondaryButton}
              variant="secondary"
            >
              {secondaryButton.text}
            </Button>
          )}
          {primaryButton && (
            <Button
              id="modal-primary-btn"
              data-testid="modal-primary-btn"
              {...primaryButton}
              className={classes.primaryButton}
            >
              {primaryButton.text}
            </Button>
          )}
        </section>
      </section>
    </Modal>
  );
};

export default AdvancedModal;
