/**
 * `useCoords` is a thin wrapper around Google's Geocoding API. It provides a
 * single `getCoords` method that takes a partial `Address` and returns a
 * `Coords[]` array with `lat` and `lng`.
 *
 * Requires a `<GeoProvider />` context with valid API keys.
 *
 * Example
 *
 * ```
 * const App = (props) => {
 *   const { coords, getCoords } = useCoords();
 *
 *   useEffect(() => {
 *     getCoords(props.address);
 *   }. [getCoords]);
 *
 *   return (
 *     <p>Lat: {coords[0]?.lat}, Lng: {coords[0]?.lng}</p>
 *   );
 * };
 * ```
 */

import {
  Address,
  Coords,
  GeoContext,
  stringifyAddress,
} from "providers/GeoProvider";
import { useCallback, useContext, useState } from "react";
import { retrieveCookie, storeCookie } from "./useCookie";
import useCrawlerDetector from "./useCrawlerDetector";

// Used to avoid geolocating accidentally during E2E tests
const mockCoords: Coords[] = [
  {
    lat: 0,
    lng: 0,
  },
];

const useCoords = () => {
  const [coords, setCoords] = useState<null | Coords[]>(null);
  const { google } = useContext(GeoContext);
  const { isCrawler } = useCrawlerDetector();

  const getCoords = useCallback(
    async (address: Partial<Address>): Promise<Coords[]> => {
      if (!google) {
        throw Error("Google API not loaded.");
      }

      const addrStr = stringifyAddress(address);
      const cacheKey = `coords-${addrStr.replace(", ", "-")}`;
      const cache = retrieveCookie<Coords[]>(cacheKey);
      if (cache) {
        setCoords(cache);
        return cache;
      } else if (
        (typeof window !== "undefined" && window.localStorage.getItem("PW")) ||
        isCrawler
      ) {
        setCoords(mockCoords);
        return mockCoords;
      }

      const geocoder = new google.maps.Geocoder();
      const { results } = await geocoder.geocode({
        address: addrStr,
      });
      if (results.length > 0) {
        const coords: Coords[] = results.map((result) => {
          const { lat, lng } = result.geometry.location;
          return { lat: lat(), lng: lng() };
        });
        setCoords(coords);
        storeCookie(cacheKey, coords);
        return coords;
      } else {
        throw new Error("No geocode results found.");
      }
    },
    [google, isCrawler],
  );

  return {
    coords,
    getCoords,
  };
};

export default useCoords;
export type { Coords };
export { mockCoords };
