import lottie, {
  AnimationConfigWithData,
  AnimationDirection,
  AnimationEventCallback,
  AnimationEventName,
  AnimationItem,
  AnimationSegment,
} from "lottie-web";
import { useEffect, useRef, useState } from "react";
import useStyles from "./Lottie.styles";

export type AnimationOptions = Pick<
  AnimationConfigWithData,
  "animationData" | "autoplay" | "loop" | "renderer" | "rendererSettings"
>;

export type AnimationEventListener = {
  eventName: AnimationEventName;
  callback: AnimationEventCallback;
};

export type LottieProps = {
  ariaLabel?: string;
  ariaRole?: string;
  className?: string;
  direction?: AnimationDirection;
  eventListeners?: AnimationEventListener[];
  height?: number | string;
  isClickToPauseDisabled?: boolean;
  isPaused?: boolean;
  isStopped?: boolean;
  options: AnimationOptions;
  segments?: AnimationSegment[];
  speed?: number;
  tabIndex?: number;
  title?: string;
  width?: number | string;
};

const emptySegments: AnimationSegment[] = [];

/**
 * A React component for displaying [Lottie](https://github.com/airbnb/lottie-web) animations.
 *
 * ## Usage
 * To display a Lottie animation simply proide the animation data JSON,
 * either via options.animationData (e.g. via import).
 *
 * ### Example: Using options.animationData (Recommended method)
 * ```typescript
 * import animationData from "path/to/animation.json";
 *
 * <Lottie options={{animationData}} />
 * ```
 *
 * Please refer to the [lottie-web](https://github.com/airbnb/lottie-web)
 * library docs for more info on functionality and customization
 * (e.g. [event listeners](https://github.com/airbnb/lottie-web#events)).
 */

const Lottie = ({
  ariaLabel = "animation",
  ariaRole = "img",
  className,
  direction,
  eventListeners,
  height = "100%",
  isClickToPauseDisabled = true,
  isStopped = false,
  isPaused = false,
  options: animationConfig,
  segments = emptySegments,
  speed = 1,
  tabIndex = -1,
  title,
  width = "100%",
}: LottieProps) => {
  const animationRef = useRef<AnimationItem>();
  const { classes, cx } = useStyles({ width, height });
  const containerRef = useRef<HTMLDivElement>(null);
  const [animationEventListeners, setAnimationEventListeners] = useState<
    AnimationEventListener[] | undefined
  >(eventListeners);

  // create the animation (or destroy & recreate the animation if the options are changed)
  useEffect(() => {
    if (!containerRef.current) {
      return;
    }

    if (animationRef.current) {
      animationRef.current?.destroy();
    }

    const config: AnimationConfigWithData = {
      animationData: animationConfig.animationData,
      autoplay: animationConfig.autoplay ?? true,
      container: containerRef.current,
      initialSegment: segments.length ? segments[0] : undefined,
      loop: animationConfig.loop ?? true,
      renderer: animationConfig.renderer,
      rendererSettings: animationConfig.rendererSettings,
    };

    animationRef.current = lottie.loadAnimation(config);

    return () => animationRef.current?.destroy();
  }, [
    animationConfig.animationData,
    animationConfig.autoplay,
    animationConfig.loop,
    animationConfig.renderer,
    animationConfig.rendererSettings,
    segments,
  ]);

  // sync event listener prop with local state
  useEffect(() => {
    setAnimationEventListeners((previousEventListeners) => {
      // unregister any previously registered event listeners when event listeners are changed
      if (previousEventListeners) {
        unregisterEventListeners(previousEventListeners);
      }

      return eventListeners;
    });
  }, [eventListeners]);

  // register event listeners when updated
  useEffect(() => {
    if (!animationRef.current || !animationEventListeners) {
      return;
    }

    registerEventListeners(animationEventListeners);

    return () => unregisterEventListeners(animationEventListeners);
  }, [animationEventListeners]);

  // update playback state when isStopped prop is changed
  useEffect(() => {
    if (!animationRef.current) {
      return;
    }

    if (isStopped) {
      animationRef.current.stop();
    } else if (!animationRef.current.isPaused) {
      segments.length
        ? animationRef.current.playSegments(segments)
        : animationRef.current.play();
    }
  }, [isStopped, segments]);

  // update playback state when isPaused prop is changed
  useEffect(() => {
    if (!animationRef.current) {
      return;
    }

    if (isPaused && !animationRef.current.isPaused) {
      animationRef.current.pause();
    } else if (!isPaused && animationRef.current.isPaused && !isStopped) {
      animationRef.current.play();
    }
  }, [isPaused, isStopped]);

  // update playback state when segments prop is changed
  useEffect(() => {
    if (!animationRef.current || !segments) {
      return;
    }

    if (segments.length && !isStopped && !animationRef.current.isPaused) {
      animationRef.current.playSegments(segments);
    }
  }, [isStopped, segments]);

  // update playback state when speed prop is changed
  useEffect(() => {
    animationRef.current?.setSpeed(speed);
  }, [speed]);

  // update playback state when direction prop is changed
  useEffect(() => {
    if (direction) {
      animationRef.current?.setDirection(direction);
      animationRef.current?.play();
    }
  }, [direction]);

  const handleClick = () => {
    if (!animationRef.current || isClickToPauseDisabled) {
      return;
    }

    togglePlayPause();
  };

  const togglePlayPause = () => {
    if (!animationRef.current) {
      return;
    }

    if (animationRef.current.isPaused) {
      animationRef.current.play();
    } else {
      animationRef.current.pause();
    }
  };

  const registerEventListeners = (eventListeners: AnimationEventListener[]) => {
    if (!containerRef.current) {
      return;
    }
    eventListeners.forEach((eventListener) =>
      animationRef.current?.addEventListener(
        eventListener.eventName,
        eventListener.callback,
      ),
    );
  };

  const unregisterEventListeners = (
    eventListeners: AnimationEventListener[],
  ) => {
    if (!containerRef.current) {
      return;
    }
    eventListeners.forEach((eventListener) =>
      animationRef.current?.removeEventListener(
        eventListener.eventName,
        eventListener.callback,
      ),
    );
  };

  return (
    <div
      aria-label={ariaLabel}
      className={cx(classes.animation, className)}
      onClick={handleClick}
      ref={containerRef}
      role={ariaRole}
      tabIndex={tabIndex}
      title={title}
    />
  );
};

export default Lottie;
