import { Theme } from "@mui/material/styles";
import { makeStyles } from "tss-react/mui";

type StyleProps = {
  padding: "small" | "medium" | "large";
  colorVariant:
    | "neutral-0"
    | "primary-50"
    | "primary-75"
    | "primary-100"
    | "secondary-50"
    | "secondary-75"
    | "secondary-100"
    | "tertiary-100"
    | "tertiary-200"
    | "tertiary-300"
    | "quaternary-75"
    | "quaternary-100"
    | "quaternary-200";
  elevated: boolean;
  hasBorder: boolean;
};

export const useStyles = makeStyles<StyleProps>({ name: "SimpleCard" })(
  (theme: Theme, { padding, colorVariant, elevated, hasBorder }) => {
    const colorVariantMap = {
      "neutral-0": theme.colors.neutral[0],
      "primary-50": theme.colors.primary[50],
      "primary-75": theme.colors.primary[75],
      "primary-100": theme.colors.primary[100],
      "secondary-50": theme.colors.secondary[50],
      "secondary-75": theme.colors.secondary[75],
      "secondary-100": theme.colors.secondary[100],
      "tertiary-100": theme.colors.tertiary[100],
      "tertiary-200": theme.colors.tertiary[200],
      "tertiary-300": theme.colors.tertiary[300],
      "quaternary-75": theme.colors.quaternary[75],
      "quaternary-100": theme.colors.quaternary[100],
      "quaternary-200": theme.colors.quaternary[200],
    };

    const paddingMap = {
      small: 1.5,
      medium: 2,
      large: 3,
    };

    return {
      card: {
        width: "100%",
        padding: theme.spacing(paddingMap[padding]),
        backgroundColor:
          colorVariantMap[colorVariant] || theme.palette.background.default,
        boxShadow: elevated ? theme.shadows[2] : "none",
        border: hasBorder ? `1px solid ${theme.colors.neutral[300]}` : "none",
      },
    };
  },
);
