/**
 * `useGeo` v2 provides transparent access to a users estimated location
 * based on a priority hierarchy including user override, home address, IP
 * geolocation and browser geolocation. The geolocation and geocoding is handled
 * using Google's Geolocation and Geocoding APIs.
 *
 * Requires a `<GeoProvider />` context with valid API keys.
 *
 * Provided values:
 *   - locations: An array of coords and partial addresses that most closely match the
 *     request.
 *   - getLocations: A method for getting a list of locations based on the
 *     current available priorities.
 *   - method: String representing the method for lookup
 *   - userOverride: Partial<Address> provided by user to provide a more accurate
 *     location
 *   - setUserOverride: Function to allow changing the userOverride
 *
 * Default priority hierarchy (first available):
 *   1. User override
 *   2. User home address (when logged in)
 *   3. IP Geolocation
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const { homeAddress } = useUserProfile();
 *   return (
 *     <GeoProvider
 *       timeZoneApiKey={Config.timeZoneApiKey}
 *       geoApiKey={Config.geoApiKey}
 *       homeAddress={homeAddress}
 *     >
 *       <Component />
 *     </GeoProvider>
 *   );
 * };
 *
 * const Component = () => {
 *   const { locations, getLocations, userOverride, setUserOverride } =
 *     useGeo();
 *
 *   useEffect(() => {
 *     getLocations();
 *   }, []);
 *
 *   return (
 *     <>
 *       <input
 *         type="text"
 *         id="postalCode"
 *         value={userOverride.postalCode}
 *         onChange={(evt: ChangeEvent) => {
 *           setUserOveride({ postalCode: evt.currentTarget.value });
 *         }}
 *       />
 *       <h1>Primary Location:</h1>
 *       <p>{locations[0]?.postalCode}</p>
 *     </>
 *   );
 * };
 * ```
 */

import { State } from "constants/states";
import useCookie from "hooks/useCookie";
import useCrawlerDetector from "hooks/useCrawlerDetector";
import {
  Address,
  defaultPriorities,
  GeoAddress,
  GeocoderRequest,
  GeocoderResult,
  GeoContext,
  GeoMethod,
  stringifyAddress,
} from "providers/GeoProvider";
import {
  SetStateAction,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
} from "react";
import AbortError from "util/AbortError";
import { Coords } from "./useCoords";
import useMemoCompare from "./useMemoCompare";

const defTimeout = 0;

// Used to avoid geolocating accidentally during E2E tests
const mockLocation: GeoAddress = {
  city: "Nowhere",
  state: "NV",
  postalCode: "97035",
};
const geoLocateUrl = "https://www.googleapis.com/geolocation/v1/geolocate";
const failureExpireDays = 1;

/**
 * Options to configure location resolution. The `getLocations` method will try
 * each method in the priorities list until a successful result is found, or the
 * list is exhausted.
 */
type LocationOpts = {
  priorities?: GeoMethod[];
  timeout?: number;
};

/**
 * Used as a lookup table into methods for resolving a location
 */
type LocationMethods = Record<GeoMethod, () => Promise<GeoAddress[]>>;

const parseGeocodeResults = (results: GeocoderResult[]) => {
  if (results.length > 0) {
    const requirements = [
      "administrative_area_level_1", // State
      "postal_code",
    ];

    const viableResults = results.filter((result) => {
      const types = result.address_components.reduce<string[]>(
        (component_types, component) => {
          return [...component_types, ...component.types];
        },
        [],
      );
      return requirements.every((type) => types.includes(type));
    });

    if (viableResults.length > 0) {
      const locations: GeoAddress[] = viableResults.map((result) => {
        const address: GeoAddress = {};
        let sublocality;
        let neighborhood;
        let administrative_area_level_3;

        // EXPL: This mapping is not 100% accurate given the nature of
        // address complexity, but is a close enough approximation
        // given the primary use case is to take a `postalCode` or
        // `lat`/`lng` coordinates and convert it to full or nearly
        // full address.
        //
        // See https://developers.google.com/maps/documentation/geocoding/requests-reverse-geocoding
        // for a breakdown of `address_components` and what each field
        // means.
        result.address_components.forEach(
          (item: google.maps.GeocoderAddressComponent) => {
            // `types` is an array with the ordering not guaranteed
            // and contains unique elements, only one of which we care
            // about here. We try our best to map these to USPS
            // addresses.
            //
            // e.g.
            // ["locality", "political"]
            // ["political", "sublocality", "sublocality_level_1"]
            item.types.forEach((type: string) => {
              switch (type) {
                case "street_number":
                  address.line1 = item.long_name;
                  break;
                case "route":
                  address.line1 = [address.line1, item.short_name].join(" ");
                  break;
                case "locality":
                  address.city = item.long_name;
                  break;
                case "sublocality":
                  sublocality = item.long_name;
                  break;
                case "neighborhood":
                  neighborhood = item.long_name;
                  break;
                case "administrative_area_level_1":
                  address.state = item.short_name as State;
                  break;
                case "administrative_area_level_3":
                  administrative_area_level_3 = item.long_name;
                  break;
                case "postal_code":
                  address.postalCode = item.long_name;
                  break;
                default: // Don't care about this type
              }
            });
          },
        );

        // EXPL: Certain postalCodes do not return a single `locality`
        // address_component and may instead have a `sublocality` or a
        // list of valid `postcode_localities` instead.
        //
        // Example: 97035 resides in Lake Grove which overlaps with
        // Lake Oswego
        //
        // Prefer `locality`, `sublocality`, `postcode_locality[0]`,
        // `administrative_area_level_3`, `neighborhood` in order.
        //
        // Ref: https://issuetracker.google.com/issues/35825301
        if (!address.city) {
          if (sublocality) {
            address.city = sublocality;
          } else if (
            result.postcode_localities &&
            result.postcode_localities.length > 0
          ) {
            // EXPL: `postcode_localities` may contain multiple
            // values, but for the purposes of deriving an approximate
            // "city" from a `postalCode`, it doesn't particularly
            // matter which one we choose - so just choose the first
            // one.
            //
            // e.g. ["Lake Grove", "Lake Oswego"] both overlap with
            // `postalCode` 97035 - so it's not clear which city would
            // be authoritative, though google seems to prefer the
            // more localized locality (Lake Grove is an official
            // civil neighborhood of Lake Oswego).
            address.city = result.postcode_localities[0];
          } else if (administrative_area_level_3) {
            address.city = administrative_area_level_3;
          } else if (neighborhood) {
            address.city = neighborhood;
          }
        }

        const { lat, lng } = result.geometry.location;
        return {
          ...address,
          lat: lat(),
          lng: lng(),
        };
      });

      return locations;
    }
  }

  throw new Error("No geocode results found.");
};

/**
 * `useGeo` hook. Refer to the top of this page for documentation.
 */
const useGeo = ({
  priorities: initialPriorities,
  timeout = defTimeout,
}: LocationOpts = {}) => {
  const priorities = useMemoCompare(
    initialPriorities ?? defaultPriorities,
    (prev, next) => JSON.stringify(prev) === JSON.stringify(next),
  );
  const { geoApiKey, geoState, geoDispatch, homeAddress, google } =
    useContext(GeoContext);
  // Cache coords for IP to avoid geolocation costs
  const [geoIp, setGeoIp] = useCookie<null | Coords>("geoIp", null);
  const [recentlyFailed, setRecentlyFailed] = useCookie("geoFailure", false, {
    expireDays: failureExpireDays,
  });
  const { isCrawler } = useCrawlerDetector();
  const controller = useRef(new AbortController());

  const browserGeo = navigator ? navigator.geolocation : null;

  // Submits a Geocoder request to Google with requested parameters
  const geocode = useCallback(
    async (request: GeocoderRequest): Promise<GeoAddress[]> => {
      if (!google) {
        throw Error("Google API not loaded.");
      } else if (
        (typeof window !== "undefined" && window.localStorage.getItem("PW")) ||
        isCrawler
      ) {
        return [mockLocation];
      }

      const geocoder = new google.maps.Geocoder();
      const { results } = await geocoder.geocode(request);
      return parseGeocodeResults(results);
    },
    [google, isCrawler],
  );

  // Reverse Geocode address based on lng/lat
  const getLocationsByCoords = useCallback(
    async ({ lat, lng }: Coords) => geocode({ location: { lat, lng } }),
    [geocode],
  );

  // Reverse geocode address based on partial address: line1, city, postalCode
  const getLocationsByAddress = useCallback(
    async (address: Partial<Address>) => {
      const geoRequest: GeocoderRequest = {};
      // If we only have a postal code, it requires a special request to prevent
      // the postal code from being interpreted as a street address or Point of
      // Interest (POI) identifier
      if (address.postalCode && Object.keys(address).length === 1) {
        geoRequest.componentRestrictions = {
          country: "USA",
          postalCode: address.postalCode,
        };
      } else {
        geoRequest.address = stringifyAddress(address);
      }
      return geocode(geoRequest);
    },
    [geocode],
  );

  const getLocationsByBrowser = useCallback(async (): Promise<GeoAddress[]> => {
    if (!browserGeo) {
      throw new Error("Browser does not support geolocation.");
    }
    return new Promise((resolve, reject) => {
      const onGeoChange = async ({
        coords: { latitude: lat, longitude: lng },
      }: GeolocationPosition) => {
        getLocationsByCoords({ lat, lng }).then(resolve).catch(reject);
      };
      const onGeoError = (err: GeolocationPositionError) => {
        console.error(err.message);
        reject(new Error(err.message));
      };
      browserGeo.getCurrentPosition(onGeoChange, onGeoError, {
        timeout: 10000,
      });
    });
  }, [getLocationsByCoords, browserGeo]);

  const getLocationsByIp = useCallback(async () => {
    try {
      if (
        (typeof window !== "undefined" && window.localStorage.getItem("PW")) ||
        isCrawler
      ) {
        return getLocationsByBrowser();
      } else if (recentlyFailed) {
        throw new Error("Skipping getLocationsByIp due to recent failure.");
      } else if (geoIp) {
        // If geoIp is cached, use it instead of a new fetch
        const locations = await getLocationsByCoords(geoIp);
        if (locations.length > 0) {
          const { lat, lng } = locations[0];
          if (lat && lng) {
            setGeoIp({ lat, lng });
          }
        }
        return locations;
      } else {
        if (!geoApiKey) {
          throw new Error("Geolocation API Key is not defined");
        }
        const reqUrl = new URL(geoLocateUrl);
        reqUrl.searchParams.set("key", geoApiKey);
        const res = await fetch(reqUrl.toString(), {
          method: "POST",
        });
        if (res.ok) {
          const data = await res.json();
          return getLocationsByCoords(data.location);
        } else {
          const json = await res.json().catch(() => "JSON parse failed");
          throw new Error(
            `Didn't receive a 2xx response from ${geoLocateUrl}. ${JSON.stringify(
              json,
            )}`,
          );
        }
      }
    } catch (err: unknown) {
      setRecentlyFailed(true);
      if (err instanceof Error) {
        console.error(err.message);
      }
      throw err;
    }
  }, [
    isCrawler,
    recentlyFailed,
    geoIp,
    getLocationsByBrowser,
    getLocationsByCoords,
    setGeoIp,
    geoApiKey,
    setRecentlyFailed,
  ]);

  const locationMethods: LocationMethods = useMemo(
    () => ({
      user_override: async () => {
        if (geoState.userOverride?.postalCode) {
          const locations = await getLocationsByAddress(geoState.userOverride);
          return locations;
        }
        throw new Error("User override postalCode not set.");
      },
      user_address: async () => {
        if (homeAddress && homeAddress.postalCode) {
          return [homeAddress];
        }
        throw new Error("User home address unavailable or postalCode not set.");
      },
      ip_address: getLocationsByIp,
      browser: getLocationsByBrowser,
    }),
    [
      getLocationsByBrowser,
      geoState.userOverride,
      getLocationsByAddress,
      homeAddress,
      getLocationsByIp,
    ],
  );

  // Ensures location requests are cancelled automatically when this hook
  // unmounts.
  useEffect(() => {
    const abortController = controller.current;
    controller.current = new AbortController();
    return () => {
      abortController.abort();
    };
  }, []);

  const getLocations = useCallback(
    async (address?: GeoAddress): Promise<GeoAddress[]> => {
      const timer =
        timeout > 0 && setTimeout(() => controller.current.abort(), timeout);

      // Utility method to handle dispatching state with locations results
      const handleResult = (method: GeoMethod, locations: GeoAddress[]) => {
        if (timer) {
          clearTimeout(timer);
        }
        if (controller.current.signal.aborted) {
          throw new AbortError("Geocode request cancelled.");
        }
        geoDispatch(["setLocations", { method, locations }]);
        return locations;
      };

      if (geoState.locations.length > 0) {
        // If cache is populated, just return it
        if (timer) {
          clearTimeout(timer);
        }
        return geoState.locations;
      } else if (address?.lat && address?.lng) {
        // If lat/lng provided in method call - skip priorities
        const { lat, lng } = address;
        return handleResult(
          "user_address",
          await getLocationsByCoords({ lat, lng }),
        );
      } else if (address?.postalCode) {
        // If postalCode provided in method call - skip priorities
        return handleResult(
          "user_address",
          await getLocationsByAddress(address),
        );
      }

      // Iterates over current priorities and chains the returned promises.
      // If a given method fails, try the next method available
      return priorities.reduce<Promise<GeoAddress[]>>(
        async (promise, method) => {
          return promise.catch(async () => {
            return handleResult(method, await locationMethods[method]());
          });
        },
        Promise.reject(new Error("No priorities were defined!")),
      );
    },
    [
      geoDispatch,
      geoState.locations,
      getLocationsByAddress,
      getLocationsByCoords,
      locationMethods,
      priorities,
      timeout,
    ],
  );

  const updateUserOverride = useCallback(
    (newValue: SetStateAction<null | GeoAddress>) => {
      const userOverride =
        newValue instanceof Function
          ? newValue(geoState.userOverride)
          : newValue;
      geoDispatch(["setUserOverride", { userOverride }]);
    },
    [geoDispatch, geoState.userOverride],
  );

  return {
    locations: geoState.locations,
    getLocations,
    method: geoState.method,
    userOverride: geoState.userOverride,
    setUserOverride: updateUserOverride,
  };
};

export default useGeo;
