import { Chip, ChipProps as MuiChipProps } from "@mui/material";
import { CloseIcon } from "assets/icons";
import { Typography } from "comp-lib";
import useStyles from "./Chip.styles";

export type ChipColors =
  | "primary"
  | "secondary"
  | "tertiary"
  | "quaternary"
  | "neutral"
  | "success"
  | "error"
  | "warning";

export type ChipProps = Omit<MuiChipProps, "color"> & {
  rounded?: boolean;
  color?: ChipColors;
  ariaLabel?: string;
};

const DeleteIconContainer = () => {
  const { classes } = useStyles({});
  return (
    <div data-testid={"delete-icon"} className={classes.deleteIcon}>
      <CloseIcon />
    </div>
  );
};

const FilterChip = (props: ChipProps) => {
  const { label, onDelete, rounded = true, size, color = "primary" } = props;
  const muiChipProps: MuiChipProps = {
    ...props,
    color: undefined,
  };
  const { classes } = useStyles({ rounded });

  return (
    <Chip
      {...muiChipProps}
      label={
        <Typography variant={size === "small" ? "microcopy" : "bodySmallBold"}>
          {label}
        </Typography>
      }
      onDelete={onDelete}
      onClick={onDelete}
      classes={{
        root: classes.root,
        label: classes.label,
        sizeMedium: classes.medium,
        sizeSmall: classes.small,
      }}
      className={classes[color]}
      deleteIcon={<DeleteIconContainer />}
      aria-label={props.ariaLabel ? props.ariaLabel : `remove ${label} filter`}
      data-testid={`${label}-chip`}
    />
  );
};

export default FilterChip;
