import MuiButton, { ButtonProps as MuiButtonProps } from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import { forwardRef, ReactNode, Ref } from "react";
import useStyles, {
  useCircularProgressStyles,
  useLoadingStyles,
} from "./Button.styles";

type C = React.ElementType;

export type ButtonProps = Omit<
  MuiButtonProps<C, { component?: C }>,
  "size" | "variant"
> & {
  size?: "xsmall" | "small" | "medium" | "large";
  variant?: "primary" | "secondary" | "secondary-alt" | "tertiary";
  contentAlign?: "left" | "center" | "right";
  isLoading?: boolean;
  borderless?: boolean;
  bare?: boolean;
  children?: ReactNode;
};

const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      className,
      size = "medium",
      variant = "primary",
      contentAlign = "center",
      isLoading = false,
      borderless = false,
      bare = false,
      ...muiButtonProps
    }: ButtonProps,
    ref: Ref<HTMLButtonElement>,
  ) => {
    const { classes, cx } = useStyles();
    const { classes: loadingClasses } = useLoadingStyles();
    const { classes: circularProgressStyles } = useCircularProgressStyles();
    const hasIcon = muiButtonProps.startIcon || muiButtonProps.endIcon;

    const children = isLoading ? (
      <>
        <div className={classes.isLoadingChildren}>
          {muiButtonProps.children}
        </div>
        <CircularProgress
          data-testid="circular-button-loader"
          className={circularProgressStyles[variant]}
          size={16}
        />
      </>
    ) : (
      <span className="button-text">{muiButtonProps.children}</span>
    );

    return (
      <MuiButton
        className={cx([
          classes.base,
          classes[`${contentAlign}Align`],
          classes[size],
          classes[variant],
          borderless && classes.borderless,
          bare && classes.bare,
          hasIcon && "has-icon",
          className,
          isLoading && loadingClasses[variant],
        ])}
        ref={ref}
        {...muiButtonProps}
        children={children}
        disabled={isLoading || muiButtonProps.disabled}
      />
    );
  },
);

export default Button;
