import {
  ToggleButtonGroup,
  ToggleButtonGroupProps,
  ToggleButtonProps,
} from "@mui/material";
import { ToggleBtn } from "comp-lib/ToggleBtn";
import React, { useEffect, useState } from "react";
import useStyles from "./SegmentedControl.styles";

const SEGMENTED_CONTROL_VERSION = "1.0.0";

type SegmentedControlProps = ToggleButtonGroupProps & {
  options: ToggleButtonProps[];
  defaultOption?: number;
};

const SegmentedControl = ({
  options,
  onChange,
  defaultOption = 1,
  classes,
  ...rest
}: SegmentedControlProps) => {
  const { classes: segmentedControlClasses, cx } = useStyles();
  const [activeSegment, setActiveSegment] = useState(defaultOption);

  const handleSegmentSelection = (
    event: React.MouseEvent<any>,
    newValue: number,
  ) => {
    if (!newValue) {
      return;
    } else {
      setActiveSegment(newValue);
      if (onChange) {
        onChange(event, newValue);
      }
    }
  };

  useEffect(() => {
    if (activeSegment !== defaultOption) {
      setActiveSegment(defaultOption);
    }
  }, [activeSegment, defaultOption]);

  return (
    <ToggleButtonGroup
      classes={{
        root: cx([segmentedControlClasses.container]),
        ...classes,
      }}
      exclusive
      onChange={handleSegmentSelection}
      value={activeSegment}
      {...rest}
    >
      {options.map((option: ToggleButtonProps, i) => {
        const { value, children } = option;
        return (
          <ToggleBtn key={i} value={value}>
            {children}
          </ToggleBtn>
        );
      })}
    </ToggleButtonGroup>
  );
};

export default SegmentedControl;
export type { SegmentedControlProps };
export { SEGMENTED_CONTROL_VERSION };
