import MUITabs, { TabsProps as MUITabsProps } from "@mui/material/Tabs";
import { ChangeEvent } from "react";
import useStyles from "./Tabs.styles";

type TabsProps = Omit<MUITabsProps, "onChange"> & {
  // Becuase of an issue with the TabsProps from Material UI (https://github.com/mui/material-ui/issues/17454),
  // onChange needs to be reimplelemnted here.
  onChange?: (event: ChangeEvent<{}>, value: any) => void;
  size?: "small" | "medium" | "large";
};

const Tabs = ({
  children,
  classes,
  className,
  size = "medium",
  ...rest
}: TabsProps) => {
  const { classes: tabClasses, cx } = useStyles();

  return (
    <MUITabs
      classes={{
        root: tabClasses.root,
        indicator: tabClasses.indicator,
        ...classes,
      }}
      className={cx(tabClasses[size], tabClasses.base, className)}
      {...rest}
    >
      {children}
    </MUITabs>
  );
};

export default Tabs;
