/**
 * `useToggle` is a simple boolean toggle hook that returns a `boolean` and a
 * toggle function.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const [isChanged, setIsChanged] = useToggle();
 *
 *   return (
 *     <button onClick={setIsChanged}>
 *       {isChanged ? "Changed!" : "Click to Change"}
 *     </button>
 *   );
 * };
 * ```
 *
 * Credit: https://usehooks.com/useToggle/
 */

import { useCallback, useState } from "react";

const useToggle = (initialState = false): [boolean, () => void] => {
  const [state, setState] = useState(initialState);

  const toggle = useCallback(() => setState((state) => !state), []);

  return [state, toggle];
};

export default useToggle;
