/**
 * `useStorage` is a hook for managing `localStorage` and `sessionStorage` as
 * component state. It is similar to `useState` except that it maintains the
 * state in browser storage instead allowing the state to persist through page
 * refreshes. It supports a server-rendered environment as well, where these
 * storage mechanisms don't exist.
 *
 * To clear a value entirely (remove from storage), set value to `null`.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   const [name, setName] = useStorage("name", "bob", {
 *     storageArea: "session",
 *   });
 *
 *   return (
 *     <div>
 *       <input
 *         type="text"
 *         placeholder="Enter your name"
 *         value={name}
 *         onChange={(evt) => setName(evt.currentTarget.value)}
 *       />
 *     </div>
 *   );
 * };
 * ```
 *
 * Credit: https://usehooks.com/useLocalStorage/
 */

import { Dispatch, SetStateAction, useCallback, useState } from "react";
import useEventListener from "./useEventListener";

const defStorageArea = "local";
const defSync = false;

type StorageOpts = {
  storageArea?: "local" | "session";
  sync?: boolean;
};

type UseStorageResult<Data> = [Data, Dispatch<SetStateAction<Data>>];

const useStorage = <Data>(
  key: string,
  initialValue?: Data,
  { storageArea = defStorageArea, sync = defSync }: StorageOpts = {},
): UseStorageResult<Data> => {
  const getStorage = useCallback(
    () =>
      storageArea === "local" ? window.localStorage : window.sessionStorage,
    [storageArea],
  );

  const [storedValue, setStoredValue] = useState<Data>(() => {
    if (typeof window === "undefined") {
      return initialValue;
    }
    try {
      const item = getStorage().getItem(key);
      return item ? JSON.parse(item) : initialValue;
    } catch (err: unknown) {
      if (err instanceof Error) {
        console.error(err.message);
      }
      return initialValue;
    }
  });

  useEventListener("storage", (evt: StorageEvent) => {
    if (sync && evt.storageArea === getStorage() && evt.key === key) {
      try {
        setStoredValue(evt.newValue ? JSON.parse(evt.newValue) : evt.newValue);
      } catch (err: unknown) {
        if (err instanceof Error) {
          console.error(err.message);
        }
      }
    }
  });

  const setValue: Dispatch<SetStateAction<Data>> = useCallback(
    (newValue) => {
      try {
        setStoredValue((currentValue) => {
          const result =
            newValue instanceof Function ? newValue(currentValue) : newValue;
          if (typeof window !== "undefined") {
            const storage = getStorage();
            result
              ? storage.setItem(key, JSON.stringify(result))
              : storage.removeItem(key);
          }
          return result;
        });
      } catch (err: unknown) {
        if (err instanceof Error) {
          console.error(err.message);
        }
      }
    },
    [getStorage, key],
  );

  return [storedValue, setValue];
};

export default useStorage;
export type { StorageOpts };
