import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Paper from "@mui/material/Paper";
import { CloseIcon } from "assets/icons";
import { Button, ButtonProps } from "comp-lib/Button";
import { IconButton } from "comp-lib/IconButton";
import { Typography } from "comp-lib/Typography";
import { ReactNode } from "react";
import {
  useFooterStyles,
  useModalStyles,
  usePaperRootStyles,
  useScrollContainerStyles,
  useTitleStyles,
} from "./Modal.styles";

export const modalMaxWidth = {
  large: "680px",
  medium: "540px", // default modal size
  small: "480px",
  exSmall: "350px",
};

export type ModalProps = Omit<React.HTMLAttributes<HTMLDivElement>, "title"> & {
  callToActionButtons?: [ButtonProps, ButtonProps?]; // buttons used in modal footer; excludes close action button at bottom of main content
  children?: null | JSX.Element | (null | JSX.Element)[];
  classes?: Partial<Record<"modalBody" | "body" | "footer", string>>;
  closeActionText?: string;
  closeActionStyle?: string | { [key: string]: any };
  contentBackgroundColor?: string;
  disableIconClose?: boolean; // hides the X in the header
  hideCloseAction?: boolean; // hides the tertiary-style "Close" button
  hideHeader?: boolean;
  contentRef?: any;
  onClose: (evt?: object, reason?: string) => void;
  open: boolean;
  scroll?: boolean; // adds right-padding to better accommodate scrollbar (overflow is set to 'auto' by default)
  size?: "large" | "medium" | "small" | "exSmall";
  title?: ReactNode;
  titleImage?: {
    src: string;
    alt?: string;
  };
  transition?: any;
  centerButton?: boolean;
};

const Modal = ({
  callToActionButtons,
  children,
  classes,
  closeActionStyle,
  closeActionText,
  contentBackgroundColor,
  disableIconClose,
  hideCloseAction = true,
  hideHeader,
  contentRef,
  onClose,
  open,
  scroll,
  size = "medium",
  title,
  titleImage,
  transition,
  centerButton,
  ...props
}: ModalProps): JSX.Element => {
  // if close action text is sent through, assume that we should show the close text button
  if (closeActionText) {
    hideCloseAction = false;
    // if no custom action text was sent through, default to close,
    // after we've determined if we should set the close action button to show or not
  } else {
    closeActionText = "Close";
  }
  const { classes: modalClasses, cx } = useModalStyles();
  const { classes: paperRootClass } = usePaperRootStyles({
    contentBgColor: contentBackgroundColor,
    maxWidth: modalMaxWidth[size],
  });
  const { classes: scrollClass } = useScrollContainerStyles();

  const ModalTitle = () => {
    const { classes: titleClass } = useTitleStyles();

    return (
      <Paper elevation={0} square>
        <DialogTitle
          id={(props.id ?? "modal") + "Title"}
          data-testid={(props.id ? props.id : "modal") + "-title"}
          classes={{ root: titleClass.titleRoot }}
        >
          {titleImage && (
            <img
              src={titleImage.src}
              alt={titleImage.alt}
              className={titleClass.titleImage}
            />
          )}
          {title && (
            <Typography
              className={titleClass.titleText}
              variant="overlineSmall"
            >
              {title}
            </Typography>
          )}
          {!disableIconClose && (
            <IconButton
              id="close-modal-btn"
              data-testid="close-modal-btn"
              aria-label="close"
              borderless
              className={titleClass.closeIconStyles}
              onClick={onClose}
              variant="secondary-alt"
              size="large"
            >
              <CloseIcon />
            </IconButton>
          )}
        </DialogTitle>
      </Paper>
    );
  };

  const ModalCloseAction = (): JSX.Element => {
    return (
      <DialogActions
        classes={{ root: cx([modalClasses.dialogAction, closeActionStyle]) }}
      >
        <Button
          id="close-action-btn"
          align="center"
          classes={{
            root: modalClasses.closeButton,
          }}
          disableRipple
          onClick={onClose}
          size="small"
          variant="tertiary"
          className={modalClasses.closeButtonSmall}
        >
          {closeActionText}
        </Button>
      </DialogActions>
    );
  };

  // Footer is below the horizontal divider at bottom of component
  // Most modals will NOT use a footer
  const ModalFooter = (): JSX.Element => {
    const { classes: footerClasses } = useFooterStyles();
    const button1 = callToActionButtons ? callToActionButtons[0] : null;
    const button2 =
      callToActionButtons && callToActionButtons.length > 1
        ? callToActionButtons[1]
        : null;

    return (
      <section
        className={cx(
          button2 && footerClasses.dualButtonFooter,
          centerButton && footerClasses.centerButton,
          size === "large" && footerClasses.largeModalFooterPadding,
          footerClasses.footer,
          classes?.footer,
        )}
        {...props}
      >
        <Button
          {...button1}
          className={footerClasses.tertiaryButton}
          variant={button1?.variant || "tertiary"}
          size={button1?.size || "small"}
        >
          {button1?.text}
        </Button>
        {button2 && (
          <Button
            {...button2}
            className={footerClasses.secondButton}
            size={button2?.size || "small"}
          >
            {button2?.text}
          </Button>
        )}
      </section>
    );
  };

  return (
    <Dialog
      aria-labelledby="modal-title"
      aria-describedby="modal-content"
      aria-modal="true"
      fullWidth
      onClose={onClose}
      open={open}
      role="dialog"
      TransitionComponent={transition ? transition : undefined}
      PaperProps={{
        id: props.id ? props.id : "modal",
      }}
      BackdropProps={{ style: { backgroundColor: "rgb(53 53 53 / 50%)" } }}
      classes={{
        paper: paperRootClass.paperRoot,
      }}
    >
      {!hideHeader && <ModalTitle />}

      <section
        tabIndex={0}
        className={modalClasses.contentContainer}
        ref={contentRef}
      >
        <section
          className={cx(
            modalClasses.modalBody,
            scroll && scrollClass[size],
            size === "large" && modalClasses.largeModalPadding,
            size === "small" && modalClasses.smallModalPadding,
            classes?.body,
            classes?.modalBody,
          )}
        >
          <DialogContent
            classes={{ root: modalClasses.modalContent }}
            id={(props.id ?? "modal") + "Content"}
          >
            {children}
          </DialogContent>
          {!hideCloseAction && <ModalCloseAction />}
        </section>
      </section>

      {callToActionButtons && <ModalFooter />}
    </Dialog>
  );
};

export default Modal;
