import FormControlLabel from "@mui/material/FormControlLabel";
import Radio, { RadioProps } from "@mui/material/Radio";
import React, { useCallback } from "react";
import useStyles from "./RadioButton.styles";

export type RadioButtonProps<T> = Omit<RadioProps, "onClick"> & {
  /**
   * label appearing with the option
   */
  label: string;
  /**
   * The value associated with the option. This will be provided as the argument to the onChange function
   * provided to  OutlnedRadioGroup
   */
  value: T;
  /**
   * error appearing when the control group is in an invalid state
   */
  error?: boolean;
  /**
   * Whether the field is required to have a value for validation purposes
   */
  required?: boolean;
  /**
   * Whether to show the (optional) text with the label.
   */
  hasOptionalText?: boolean;
  /**
   * Shows when the field is in an invalid (error) state
   */
  helperText?: string;
  /**
   * Callback that fires on click of the radio.
   * @param value same as the value prop provided to the button
   * @returns void
   */
  onClick: (value: T) => void;
};

export function RadioButton<T>({
  value,
  label,
  error,
  required,
  hasOptionalText,
  helperText,
  checked,
  onClick,
  ...muiRadioProps
}: RadioButtonProps<T>) {
  const { classes, cx } = useStyles();

  const labelVariations = (
    <span className={helperText ? classes.helperTextContainer : undefined}>
      <span>
        {label}
        {hasOptionalText && !required ? (
          <span className={classes.optionalText}>(Optional)</span>
        ) : required && !hasOptionalText ? (
          <span className={classes.errorColor}>*</span>
        ) : null}
      </span>
      {helperText ? (
        <span
          className={cx(classes.helperText, {
            [classes.errorColor]: error,
          })}
        >
          {helperText}
        </span>
      ) : null}
    </span>
  );

  const handleChange = useCallback(() => onClick(value), [onClick, value]);

  return (
    <FormControlLabel
      classes={{
        root: error ? classes.errorFormControl : classes.formControl,
        label: error ? classes.errorFormControlLabel : classes.formControlLabel,
      }}
      value={value}
      control={
        <Radio
          checked={checked}
          className={cx(classes.root, {
            [classes.helperRoot]: Boolean(helperText),
          })}
          disableRipple
          color="default"
          onClick={handleChange}
          checkedIcon={
            <span
              className={cx(
                classes.icon,
                error ? classes.errorIcon : classes.checkedIcon,
              )}
            />
          }
          icon={
            <span
              className={cx(classes.icon, {
                [classes.errorIcon]: Boolean(error),
              })}
            />
          }
          {...muiRadioProps}
        />
      }
      label={labelVariations}
    />
  );
}
