import FormControlLabel from "@mui/material/FormControlLabel";
import Radio, { RadioProps } from "@mui/material/Radio";
import React from "react";
import useStyles from "./CompactRadioButton.styles";

export type CompactRadioButtonProps = {
  radioButtonProps?: RadioProps;
  label: string;
  value: string;
  error?: boolean;
  required?: boolean;
  hasOptionalText?: boolean;
  helperText?: string;
};

const CompactRadioButton = ({
  radioButtonProps,
  value,
  label,
  error,
  required,
  hasOptionalText,
  helperText,
}: CompactRadioButtonProps) => {
  const { classes, cx } = useStyles();

  const labelVariations = (
    <span className={helperText ? classes.helperTextContainer : undefined}>
      <span>
        {label}
        {hasOptionalText && !required ? (
          <span className={classes.optionalText}>(Optional)</span>
        ) : required && !hasOptionalText ? (
          <span className={classes.errorColor}>*</span>
        ) : null}
      </span>
      {helperText ? (
        <span
          className={
            error
              ? cx(classes.helperText, classes.errorColor)
              : classes.helperText
          }
        >
          {helperText}
        </span>
      ) : null}
    </span>
  );

  return (
    <FormControlLabel
      classes={{
        root: error ? classes.errorFormControl : classes.formControl,
        label: error ? classes.errorFormControlLabel : classes.formControlLabel,
      }}
      value={value}
      control={
        <Radio
          className={
            helperText ? cx(classes.helperRoot, classes.root) : classes.root
          }
          disableRipple
          color="default"
          checkedIcon={
            <span
              className={
                error
                  ? cx(classes.icon, classes.errorIcon)
                  : cx(classes.icon, classes.checkedIcon)
              }
            />
          }
          icon={
            <span
              className={
                error ? cx(classes.errorIcon, classes.icon) : classes.icon
              }
            />
          }
          {...radioButtonProps}
        />
      }
      label={labelVariations}
    />
  );
};

export default CompactRadioButton;
