import { createTheme } from "@mui/material/styles";
import breakpoints from "./breakpoints";
import { Colors } from "./colors";
import createOverrides from "./overrides";
import paletteLight from "./paletteLight";
import shadows from "./shadows";

const DrivewayTheme = createTheme({
  breakpoints,
  palette: paletteLight,
  shadows: shadows,
  typography: {
    fontFamily:
      "Circular, -apple-system, BlinkMacSystemFont, Segoe UI, " +
      "Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, " +
      "Segoe UI Emoji, Segoe UI Symbol",
    htmlFontSize: 14,
  },
  colors: Colors,
});

DrivewayTheme.components = createOverrides({
  _typography: DrivewayTheme.typography,
  breakpoints: DrivewayTheme.breakpoints,
});

export default DrivewayTheme;
