import MUITab, { TabProps } from "@mui/material/Tab/";
import useStyles from "./Tab.styles";

const Tab = ({ classes, ...rest }: TabProps) => {
  const { classes: tabClasses } = useStyles();

  return (
    <MUITab
      classes={{
        root: tabClasses.root,
        selected: tabClasses.selected,
        textColorPrimary: tabClasses.textColorPrimary,
        ...classes,
      }}
      {...rest}
    />
  );
};

export default Tab;
