import FormControlLabel from "@mui/material/FormControlLabel";
import Radio, { RadioProps } from "@mui/material/Radio";
import { CheckCircleFilledIcon } from "assets/icons";
import { Typography } from "comp-lib/Typography";
import { useEffect, useState } from "react";
import useStyles from "./OutlinedRadioButton.styles";

type RadioButtonProps = {
  radioButtonProps?: RadioProps;
  mainLabel: string;
  descriptionLabel: string;
  reverseLabel?: boolean;
  withSuccessIcon?: boolean;
  value: string;
  currentSelection: string;
  size: string;
};

const OutlinedRadioButton = (props: RadioButtonProps) => {
  const {
    radioButtonProps,
    mainLabel,
    descriptionLabel,
    reverseLabel,
    withSuccessIcon,
    value,
    currentSelection,
    size,
  } = props;

  const { classes, cx } = useStyles();
  const [isFocused, setIsFocused] = useState(false);
  const [isSelected, setIsSelected] = useState(false);

  useEffect(() => {
    setIsSelected(currentSelection === value);
  }, [currentSelection, value]);

  const labelComponent = (
    <>
      <span
        className={withSuccessIcon ? classes.mainLabelContainer : undefined}
      >
        {withSuccessIcon && isSelected ? (
          <CheckCircleFilledIcon className={classes.successIcon} />
        ) : null}
        <Typography
          className={classes.mainLabel}
          variant={isSelected ? "bodyBold" : "body"}
        >
          {mainLabel}
        </Typography>
      </span>
      <Typography className={classes.descriptionLabel} variant="bodySmall">
        {descriptionLabel}
      </Typography>
    </>
  );

  useEffect(() => {
    let field = document.getElementById(value);
    let label = document.getElementById(`${value}-label`);
    if (label) {
      // prevent focus passing
      label.addEventListener("focus", (event) => {
        event.preventDefault();
      });

      // activate using spacebar
      label.addEventListener("keyup", (event) => {
        if (event.key === "Enter") {
          field?.click();
        }
      });
    }
  }, [value]);

  return (
    <FormControlLabel
      id={`${value}-label`}
      data-testid={`${value}-${descriptionLabel}-radio-button`}
      onFocus={() => setIsFocused(true)}
      onBlur={() => setIsFocused(false)}
      onClick={() => setIsFocused(false)}
      classes={{
        root: cx(
          withSuccessIcon
            ? cx(classes.formControl, classes.alignLeft)
            : classes.formControl,
          { [classes.focused]: isFocused },
          { [classes.selected]: isSelected },
          { [classes.sizeSmall]: size === "small" },
        ),
        label: reverseLabel
          ? cx(classes.formControlLabel, classes.reverseLabel)
          : classes.formControlLabel,
      }}
      disabled={radioButtonProps?.disabled}
      value={value}
      tabIndex={radioButtonProps?.tabIndex}
      control={
        <Radio
          id={value}
          className={classes.button}
          disabled={radioButtonProps?.disabled}
          disableRipple
          color="default"
          {...radioButtonProps}
        />
      }
      label={labelComponent}
    />
  );
};

export default OutlinedRadioButton;
