/**
 * `useEventListener` makes it ease to set up event listeners without the
 * `useEffect` boilerplate. Simply use it the same as `addEventListener`. The
 * third parameter allows customizing which element the listener is attached to.
 *
 * Example
 *
 * ```
 * const Add = () => {
 *   const [coords, setCoords] = useState({ x: 0, y: 0 });
 *
 *   const handler = ({ clientX, clientY }) => {
 *     setCoords({ x: clientX, y: clientY });
 *   };
 *
 *   useEventListner("mousemove", handler);
 *
 *   return (
 *     <div>
 *       Mouse position: ({coords.x}, {coords.y})
 *     </div>
 *   );
 * };
 * ```
 *
 * Credit: https://usehooks.com/useEventListener/
 */

import { RefObject, useEffect, useRef } from "react";

interface UseEventListener {
  <K extends keyof WindowEventMap>(
    eventName: K,
    handler: (evt: WindowEventMap[K]) => void,
  ): void;
  <K extends keyof HTMLElementEventMap, T extends HTMLElement = HTMLDivElement>(
    eventName: K,
    handler: (evt: HTMLElementEventMap[K]) => void,
    element: RefObject<T>,
  ): void;
}

const useEventListener: UseEventListener = <
  KW extends keyof WindowEventMap,
  KH extends keyof HTMLElementEventMap,
  T extends HTMLElement | void = void,
>(
  eventName: KW | KH,
  handler: (evt: WindowEventMap[KW] | HTMLElementEventMap[KH] | Event) => void,
  element?: RefObject<T>,
) => {
  const handlerRef = useRef(handler);

  // Ensure the handlerRef stays up to date. Done so we don't need to force
  // users to memoize their handler function.
  useEffect(() => {
    handlerRef.current = handler;
  }, [handler]);

  useEffect(() => {
    const target = element?.current ?? window;
    const isSupported = target && target.addEventListener;
    if (!isSupported) {
      return;
    }

    const listener: typeof handler = (event) => handlerRef.current?.(event);

    target.addEventListener(eventName, listener);
    return () => {
      target.removeEventListener(eventName, listener);
    };
  }, [eventName, element]);
};

export default useEventListener;
