import {
  AlertCircleIcon,
  AlertTriangleIcon,
  CheckCircleIcon,
  CloseIcon,
  InfoIcon,
} from "assets/icons";
import { IconButton } from "comp-lib/IconButton";
import { Typography } from "comp-lib/Typography";
import { HTMLAttributes, ReactNode, SyntheticEvent } from "react";
import useStyles from "./Banner.styles";

export type BannerProps = HTMLAttributes<HTMLDivElement> & {
  id?: string;
  variant: "error" | "success" | "attention" | "neutral" | "info";
  message: ReactNode;
  closeBanner?: (e: SyntheticEvent) => void;
  onClick?: () => void;
  isInline?: boolean;
  disableCloseIcon?: boolean;
  disableStatusIcon?: boolean; // deprecated
  justifyContentCenter?: boolean; // deprecated
};

/**
   *
   ## Banner Usage
    The **Banner** component can be used to display success or
    failure information either above the main header of Driveway.com, i.e.,
    the header in the **Layout** component, or inline in a component.
  **/

const Banner = ({
  id,
  variant,
  message,
  closeBanner,
  onClick,
  isInline = false,
  disableCloseIcon = false,
  disableStatusIcon = false,
  justifyContentCenter = false,
  ...props
}: BannerProps) => {
  const { classes, cx } = useStyles();
  if (!message) {
    return null;
  }

  let variantClass, BannerIcon;

  switch (variant) {
    case "error":
      variantClass = classes.warning;
      BannerIcon = () => <AlertCircleIcon />;
      break;
    case "success":
      variantClass = classes.success;
      BannerIcon = () => <CheckCircleIcon />;
      break;
    case "attention":
      variantClass = classes.attention;
      BannerIcon = () => <AlertTriangleIcon />;
      break;
    case "info":
      variantClass = classes.info;
      BannerIcon = () => <InfoIcon />;
      break;
    case "neutral":
    default:
      variantClass = classes.neutral;
      BannerIcon = () => <InfoIcon />;
  }

  return (
    <div
      id={id}
      onClick={onClick}
      className={cx([
        classes.banner,
        variantClass,
        isInline ? classes.inline : "",
        isInline && ["error", "attention", "warning"].includes(variant)
          ? classes.inlineMargin
          : "",
        disableCloseIcon ? "" : classes.bannerClose,
        onClick ? classes.hover : "",
        justifyContentCenter ? classes.centerContent : "",
      ])}
      {...props}
    >
      {!disableStatusIcon && (
        <div className={classes.msgIcon}>
          <BannerIcon />
        </div>
      )}
      <Typography className={classes.bannerMsg} variant={"body"}>
        {message}
      </Typography>
      {disableCloseIcon === false && (
        <IconButton
          className={classes.closeIconButton}
          aria-label="close"
          borderless
          onClick={closeBanner}
          variant="secondary"
          size="small"
        >
          <CloseIcon className={classes.closeIconBlack} />
        </IconButton>
      )}
    </div>
  );
};

export default Banner;
