/**
 * `useTimer` is a simple timer hook that calls a callback after a period of
 * time.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   useTimer(5000, () => {
 *      console.log("5 second timer has expired!");
 *   });
 *
 *   return null;
 * };
 * ```
 */

import { useEffect } from "react";

const useTimer = (milliseconds: number, onTimeout: () => void) => {
  useEffect(() => {
    const timer = setTimeout(onTimeout, milliseconds);
    return () => clearTimeout(timer);
  }, [milliseconds, onTimeout]);
};

export default useTimer;
