import { makeStyles } from "tss-react/mui";

type AnimationStyleProps = {
  width: number | string;
  height: number | string;
};

const useStyles = makeStyles<AnimationStyleProps>({ name: "MuiDWLoadingBar" })(
  (_theme, { width, height }) => ({
    animation: {
      width: width,
      height: height,
      margin: "0 auto",
      overflow: "hidden",
      outline: "none",
    },
  }),
);

export default useStyles;
