import { Box, BoxProps, RadioGroup } from "@mui/material";
import { Fragment, ReactNode, useCallback, useEffect, useState } from "react";
import { scrollIntoView } from "util/scrollIntoView";
import { RadioButton } from "./RadioButton";
import { RadioButtonProps } from "./RadioButton/RadioButton";
import useStyles from "./RadioButtonGroup.styles";

export type RadioButtonGroupOption<TValue> = Omit<
  RadioButtonProps<TValue>,
  "checked" | "onClick"
> & {
  subContent?: ReactNode;
};

export type RadioButtonGroupProps<TValue> = Omit<BoxProps, "onChange"> & {
  /**
   * Array of option values to be presented. Can be any type (TValue).
   * The onChange function will receive the selected options value prop when selected
   */
  options?: RadioButtonGroupOption<TValue>[];
  /**
   * Callback that fires when the selection changes.
   * The value prop (TValue) of the selected option will be passed in as the argument
   * @param value Value of the selected button
   * @returns void
   */
  onChange: (value: TValue) => void;
  /**
   * Provide this optional prop to force the group into a state with the particular value selected
   */
  selectedValue?: TValue;
  /**
   * Disables the entire group of controls
   */
  disabled?: boolean;
  /**
   * Id applied to the group
   */
  id?: string;
  /**
   * Label appearing above the whole group
   */
  groupLabel?: string;
  /**
   * Appears beneath the groupLabel
   */
  description?: string;
  /**
   * Classname applied to the group container
   */
  className?: string;
  /**
   * Message apperaing when the group is in an error state
   */
  errorText?: string;
  /**
   * Specify whether/how the group scrolls into view when focussed
   */
  scrollIntoViewOptions?: ScrollIntoViewOptions | boolean;
  classes?: Record<string, string>;
  /**
   * If provided causes a tooltip to be ready for the group
   */
  tooltip?: JSX.Element;
};

function RadioButtonGroup<TValue>({
  options,
  onChange,
  description,
  className,
  groupLabel,
  errorText,
  selectedValue: selectedValueFromProps,
  disabled,
  tooltip,
  scrollIntoViewOptions,
  classes: classesProp,
  id,
  ...wrapperProps
}: RadioButtonGroupProps<TValue>) {
  const { classes, cx } = useStyles();
  const { classes: classProps } = useStyles(undefined, {
    props: { classes: classesProp },
  });

  const [selectedValue, setSelectedValue] = useState<TValue | null>(
    selectedValueFromProps ?? null,
  );

  useEffect(() => {
    setSelectedValue(selectedValueFromProps ?? null);
  }, [selectedValueFromProps]);

  const handleClick = useCallback(
    (value: TValue) => {
      setSelectedValue(value);
      onChange(value);
    },
    [onChange],
  );

  const onFocus = (e: React.FocusEvent<HTMLDivElement>) => {
    scrollIntoView(e.currentTarget, scrollIntoViewOptions);
  };

  return (
    <Box
      className={cx([classProps.root, className])}
      onFocus={onFocus}
      {...wrapperProps}
    >
      <div className={classes.warningIconContainer}>
        <div className={disabled ? classes.disabledGroupLabel : undefined}>
          {groupLabel}
        </div>
      </div>
      <div className={errorText ? classes.errorText : classes.description}>
        {errorText || description}
        {tooltip && <div>{tooltip}</div>}
      </div>
      <RadioGroup
        id={id ?? "radioGroup"}
        value={selectedValue}
        className={cx(classes.card, {
          [classes.errorCard]: Boolean(errorText),
        })}
      >
        {options?.map((option, index) => {
          const { value, subContent, ...restProps } = option;

          return (
            <Fragment key={option.label}>
              <RadioButton
                {...restProps}
                checked={selectedValue === value}
                value={value}
                id={`${id || ""}_radio_${index}`}
                onClick={handleClick}
              />
              {selectedValue === value && subContent}
            </Fragment>
          );
        })}
      </RadioGroup>
    </Box>
  );
}

export default RadioButtonGroup;
