/**
 * `useLimitRerender` is a utility to prevent infinite render loops in React by
 * keeping track of the number of times it is rendered per second. If the count
 * exceeds the maximum, an error is thrown for the caller to handle or crash
 * the application.
 *
 * Example
 *
 * ```
 * const App = () => {
 *   useLimitRerender((renderCount) => {
 *     console.log(`Render limit reached: ${renderCount`);
 *   }, 100);
 *
 *   // Trivial example, but this is a new object every render, causing the
 *   // useEffect to fire, causing a render, infinitely until useLimitRerender
 *   // throws an error, triggering any error boundaries.
 *   const newObject = {};
 *
 *   useEffect(() => {
 *     console.log("rendering");
 *   }, [newObject]);
 *
 *   return null;
 * };
 * ```
 */

import { useEffect, useRef } from "react";

// This is an estimate and may need to be adjusted
const DEFAULT_MAX_RENDERS = 200;

type LimitRerenderCallback = (renderCount: number) => void;

const useLimitRerender = (
  callback?: LimitRerenderCallback,
  maxRenders: number = DEFAULT_MAX_RENDERS,
) => {
  const renderCount = useRef(0);

  // Credit:
  // https://medium.com/better-programming/how-to-properly-use-the-react-useref-hook-in-concurrent-mode-38c54543857b
  let renderCountCurrent = renderCount.current;
  useEffect(() => {
    if (process.env.NODE_ENV === "production") {
      // Don't operate in production as this adds some performance overhead
      return;
    }

    renderCount.current = renderCountCurrent;
    const resetTimer = setTimeout(() => {
      renderCount.current = 0;
    }, 200);
    return () => {
      clearTimeout(resetTimer);
    };
  });

  renderCountCurrent += 1;

  if (renderCountCurrent > maxRenders) {
    if (callback) {
      callback(renderCountCurrent);
    }
    throw new Error(`max renders limit of ${maxRenders} reached!`);
  }

  return renderCount.current;
};

export default useLimitRerender;
export { DEFAULT_MAX_RENDERS };
export type { LimitRerenderCallback };
