import { Lottie } from "comp-lib/Lottie";
import { makeStyles } from "tss-react/mui";
import animationData from "./spinner_Primary_500.json";

const useStyles = makeStyles({ name: "MuiDWLoadingCar" })(() => ({
  loadingSpinner: {
    maxWidth: "300px",
    margin: "0px auto",
  },
}));

export type SelectCardProps = {
  classes?: any;
  animation?: any;
};

const LoadingCarSpinner = (props: SelectCardProps) => {
  const { classes } = useStyles();
  const lottieOptions = {
    animationData: props.animation || animationData,
  };

  return (
    <div className={classes.loadingSpinner}>
      <Lottie
        options={lottieOptions}
        isClickToPauseDisabled={true}
        ariaRole="status" // https://www.w3.org/WAI/WCAG21/Techniques/aria/ARIA22.html
        ariaLabel="Loading"
        // TODO: tabIndex="-1" // this requires a fork of react-lottie AND @types/react-lottie
      />
    </div>
  );
};

export default LoadingCarSpinner;
