import {
  Pagination as MuiPagination,
  PaginationItem,
  PaginationProps as MuiPaginationProps,
} from "@mui/material";
import { ChevronLeftIcon, ChevronRightIcon } from "assets/icons";
import useStyles from "./Pagination.styles";

export type PaginationProps = MuiPaginationProps & {
  wrap?: boolean;
};

const Pagination = ({
  classes,
  count,
  siblingCount = 0,
  ...muiPaginationProps
}: PaginationProps) => {
  const { classes: paginationClasses, cx } = useStyles();

  return (
    <MuiPagination
      classes={{
        ...classes,
        root: cx(paginationClasses.root, classes?.root),
      }}
      count={count}
      data-testid="pagination"
      shape="rounded"
      siblingCount={siblingCount}
      size="large"
      variant="outlined"
      {...muiPaginationProps}
      renderItem={(item) => (
        <PaginationItem
          components={{ previous: ChevronLeftIcon, next: ChevronRightIcon }}
          className={item.type === "page" ? "button-page-nav" : undefined}
          id={
            item.type === "previous"
              ? "page-prev"
              : item.type === "next"
              ? "page-next"
              : undefined
          }
          {...item}
        />
      )}
    />
  );
};

export default Pagination;
