import { Slide, Snackbar as MUISnackbar, SnackbarContent } from "@mui/material";
import { TransitionProps } from "@mui/material/transitions";
import { Button, ButtonProps } from "comp-lib/Button";
import React, { Dispatch, ReactNode, SetStateAction } from "react";
import useStyles from "./Snackbar.styles";

type SnackbarProps = {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  message: ReactNode;
  actionBtnProps?: ButtonProps;
  onScreenDuration?: number;
  transitionDuration?: number;
  travelDirection?: "left" | "right" | "up" | "down";
  anchorOrigin?: {
    horizontal: "center" | "left" | "right";
    vertical: "bottom" | "top";
  };
};

const Snackbar = ({
  isOpen,
  setIsOpen,
  message,
  actionBtnProps,
  onScreenDuration = 6000,
  transitionDuration = 500,
  travelDirection = "up",
  anchorOrigin = { horizontal: "left", vertical: "bottom" },
}: SnackbarProps): JSX.Element => {
  const { classes } = useStyles();

  const handleClose = (
    _event: Event | React.SyntheticEvent<any, Event>,
    reason?: string,
  ) => {
    if (reason === "clickaway") {
      return;
    }
    setIsOpen(false);
  };

  const Transition = (
    props: TransitionProps & { children: React.ReactElement },
  ) => {
    return (
      <Slide
        direction={travelDirection}
        timeout={transitionDuration}
        mountOnEnter
        unmountOnExit
        {...props}
      />
    );
  };

  return (
    <MUISnackbar
      TransitionComponent={Transition}
      open={isOpen}
      autoHideDuration={onScreenDuration}
      onClose={handleClose}
      anchorOrigin={anchorOrigin}
      aria-live="assertive"
      data-testid="snackbar"
    >
      <SnackbarContent
        className={classes.container}
        message={message}
        action={
          actionBtnProps ? (
            <Button
              className={classes.btn}
              variant="tertiary"
              size="small"
              {...actionBtnProps}
            >
              {actionBtnProps.text}
            </Button>
          ) : null
        }
      />
    </MUISnackbar>
  );
};

export default Snackbar;
export type { SnackbarProps };
