import { CheckboxProps as MuiCheckboxProps } from "@mui/material";
import SimpleCheckbox from "./SimpleCheckbox";

export type CheckboxProps = MuiCheckboxProps & {
  variant: string;
  label?: string;
  active?: boolean;
  checked?: boolean;
  error?: boolean;
  required?: boolean;
  hasOptionalText?: boolean;
  helperText?: string;
};

type Element = {
  [property: string]: JSX.Element;
};

const Checkbox = (props: CheckboxProps) => {
  const {
    variant,
    label,
    checked,
    error,
    required,
    hasOptionalText,
    helperText,
    ...rest
  } = props;

  const element: Element = {
    simple: (
      <SimpleCheckbox
        label={label}
        error={error}
        required={required}
        hasOptionalText={hasOptionalText}
        helperText={helperText}
        checkboxProps={{ checked: checked, ...rest }}
      />
    ),
  };

  return element[variant];
};

export default Checkbox;
