import { Theme } from "@mui/material";
import { ButtonProps } from "comp-lib/Button";
import { AlertTypes } from "comp-lib/models/alert-types";
import { Dispatch, ReactNode, SetStateAction } from "react";

type AlertColorsType = {
  [key in AlertTypes]: React.CSSProperties["color"];
};

export const AlertColors = (theme: Theme): AlertColorsType => ({
  [AlertTypes.Warning]: theme.colors.warning[500],
  [AlertTypes.Success]: theme.colors.success[500],
  [AlertTypes.Error]: theme.colors.error[500],
  [AlertTypes.Informative]: theme.colors.primary[700],
});

export type ToastProps = {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  title?: ReactNode;
  message: ReactNode;
  actionBtnProps?: ButtonProps | null;
  alertType?: AlertTypes;
  onScreenDuration?: number | null;
  closeOnClickAway?: boolean;
  travelDirection?: "left" | "right" | "up" | "down";
  anchorOrigin?: {
    horizontal: "center" | "left" | "right";
    vertical: "bottom" | "top";
  };
  className?: string;
  id?: string;
};
