import { RadioGroup } from "@mui/material";
import { RadioButton } from "comp-lib/RadioButton";
import { useEffect, useState } from "react";
import { scrollIntoView } from "util/scrollIntoView";
import { RadioButtonGroupProps, RadioGroupOptions } from "../RadioButtonGroup";
import useStyles from "./CompactGroup.styles";

const CompactGroup = ({
  variant,
  options,
  onChange,
  groupTextContent,
  groupBooleanChecks,
  tooltip,
  scrollIntoViewOptions,
  classes: classesProp,
  id,
}: RadioButtonGroupProps) => {
  const { classes, cx } = useStyles();
  const { classes: classProps } = useStyles(undefined, {
    props: { classes: classesProp },
  });

  const [value, setValue] = useState("");

  useEffect(() => {
    setValue(groupTextContent.selectedValue || "");
  }, [groupTextContent.selectedValue]);

  const children = (
    <>
      {options?.map((option: RadioGroupOptions, index: number) => {
        const radioProps = option.radioButtonProps
          ? {
              ...option.radioButtonProps,
              id: id + "_radio_" + index,
              ...(groupBooleanChecks?.disabled && { disabled: true }),
            }
          : {
              ...(groupBooleanChecks?.disabled && { disabled: true }),
              id: id + "_radio_" + index,
            };

        return (
          <div key={option.value}>
            <RadioButton
              variant={variant}
              radioButtonProps={radioProps}
              value={option.value}
              textContent={option.textContent}
              helperText={option.helperText}
            />
            {value === option.value && option.subContent}
          </div>
        );
      })}
    </>
  );

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = (event.target as HTMLInputElement).value;
    setValue(value);
    onChange(value);
  };

  const onFocus = (e: React.FocusEvent<HTMLDivElement>) => {
    scrollIntoView(e.currentTarget, scrollIntoViewOptions);
  };

  return (
    <div
      className={cx([classProps.root, groupTextContent.className])}
      onFocus={onFocus}
    >
      <div className={classes.warningIconContainer}>
        {
          <div
            className={
              groupBooleanChecks?.disabled
                ? classes.disabledGroupLabel
                : undefined
            }
          >
            {groupTextContent.groupLabel}
          </div>
        }
      </div>
      {groupTextContent.errorText ? (
        <div className={classes.errorText}>
          {groupTextContent.errorText} {tooltip && <div>{tooltip}</div>}
        </div>
      ) : (
        <div className={classes.description}>
          {groupTextContent.description}
          {tooltip && <div>{tooltip}</div>}
        </div>
      )}
      <RadioGroup
        id={id ?? "radioGroup"}
        className={
          groupTextContent.errorText
            ? cx([classes.card, classes.errorCard])
            : classes.card
        }
        value={value}
        onChange={handleChange}
      >
        {children}
      </RadioGroup>
    </div>
  );
};

export default CompactGroup;
