// example usage:
// <input onFocus={(e) => scrollIntoView(e.target, true)} />
// <input onFocus={(e) => scrollIntoView(e.target, mdUp)} />
// <Modal onOpen={() => scrollIntoView(modalRef, true)} />

export const scrollIntoView = (
  element: HTMLElement,
  opts?: ScrollIntoViewOptions | boolean,
) => {
  // The presence of true or an object here is considered positive confirmation that
  // the implementer wants this featured turned on.
  // Not setting `scrollIntoViewOptions` to a truthy value will result in not scrolling.
  if (typeof opts === "boolean" && !opts) {
    // don't scroll if they've passed false to opts to disable
    return;
  }

  const scrollOpts =
    typeof opts === "object"
      ? opts
      : {
          block: "center",
          behavior: "smooth",
        };

  element.scrollIntoView(scrollOpts as ScrollIntoViewOptions);
};
