import { SliderThumb } from "@mui/material/Slider";
import { SliderKnobIcon } from "assets/icons";

const SliderThumbComponent = (props: any) => {
  const { children, ...rest } = props;

  return (
    <SliderThumb {...rest}>
      {children}
      <SliderKnobIcon />
    </SliderThumb>
  );
};

export default SliderThumbComponent;
